<!-- src/components/MainPage.vue -->
<template>
  <div v-if="userStore.isDataLoaded">
    <MobileDashboard v-if="isMobile" />
    <div v-else class="desktop-dashboard">
      <h2> , {{ userStore.firstName }}</h2>
      <Banner />
      <Dashboard />
      <LastNews />
    </div>
  </div>
  <div v-else class="loading">
    <p>...</p>
  </div>
</template>

<script>
import { onMounted, ref, onBeforeUnmount } from "vue";
import { useUserStore } from "@/store/user";
import Banner from "../Dashboard/Banner.vue";
import Dashboard from "../Dashboard/DashboardContent.vue";
import LastNews from "../Dashboard/LastNews.vue";
import MobileDashboard from "../Dashboard/MobileDashboard.vue";

export default {
  name: "MainPage",
  components: {
    Banner,
    Dashboard,
    LastNews,
    MobileDashboard
  },
  setup() {
    const userStore = useUserStore();
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      if (!userStore.isDataLoaded) {
        userStore.initialize();
      }
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      userStore,
      isMobile
    };
  }
};
</script>

<style scoped>
.desktop-dashboard {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  background-color: #e2f3ff;
  padding: 20px 300px;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #e2f3ff;
}

@media (max-width: 768px) {
  .desktop-dashboard {
    margin-left: 0;
    padding: 15px;
  }
}
</style>
