<template>
  <div class="mobile-news">
    <!--    -->
    <div class="search-section">
      <div class="search-box">
        <img src="@/icons/search.svg" alt="Search" class="search-icon" />
        <input
          type="text"
          v-model="searchQuery"
          placeholder=" ..."
          class="search-input"
        />
      </div>
      
      <div class="filters">
        <button
          class="filter-btn"
          :class="{ active: showFavorites }"
          @click="toggleFavoriteFilter"
        >
          <img src="@/icons/star.svg" alt="Star" />
        </button>
        <button
          class="filter-btn"
          :class="{ active: isSourcesMenuOpen }"
          @click="toggleSourcesMenu"
        >
          <img src="@/icons/filter.svg" alt="Filter" />
        </button>
      </div>
    </div>

    <!--    -->
    <div v-if="isSourcesMenuOpen" class="sources-menu">
      <div
        v-for="source in uniqueSources"
        :key="source"
        :class="['source-item', { active: selectedSources.includes(source) }]"
        @click="selectSource(source)"
      >
        {{ source }}
      </div>
      <div class="source-item" @click="selectAllSources">
         
      </div>
    </div>

    <!--   -->
    <div class="news-list">
      <!--  -->
      <div v-if="isLoading" class="loading-state">
        <div class="spinner"></div>
        <span> ...</span>
      </div>

      <!--  -->
      <div v-else-if="filteredNewsList.length > 0" class="news-items">
        <div v-for="(news, index) in filteredNewsList" 
             :key="index" 
             class="news-card">
          <div class="news-image" 
               :style="{ backgroundImage: `url(${getImageUrl(news)})` }">
          </div>
          <div class="news-content">
            <div class="news-header">
              <h3 class="news-title">
                <a :href="news.link" target="_blank" rel="noopener noreferrer">
                  {{ news.title }}
                </a>
              </h3>
              <button
                class="favorite-btn"
                :class="{ active: isNewsFavorite(news) }"
                @click="toggleFavorite(news)"
              >
                <img src="@/icons/star.svg" alt="Star" />
              </button>
            </div>
            <p class="news-description">{{ news.content }}</p>
            <div class="news-footer">
              <span class="news-source">{{ news.source }}</span>
              <span class="news-date">
                <img src="@/icons/calendar.svg" alt="Calendar" />
                {{ formatDate(news.source.toLowerCase() === "dzen" ? news.created_at : news.published_date) }}
              </span>
            </div>
          </div>
        </div>
      </div>

      <!--   -->
      <div v-else class="empty-state">
        <p>  </p>
      </div>
    </div>
  </div>
</template>

<script>
import { useNewsStore } from "@/store/news.js";
import { computed, onMounted, ref } from "vue";

export default {
  name: "MobileNews",
  setup() {
    const newsStore = useNewsStore();
    const searchQuery = ref("");
    const isSourcesMenuOpen = ref(false);
    const selectedSources = ref([]);
    const showFavorites = ref(false);
    const isLoading = ref(true);

    // ,    
    if (newsStore.newsList.length > 0) {
      isLoading.value = false;
    }

    onMounted(async () => {
      if (newsStore.newsList.length === 0) {
        try {
          await newsStore.fetchAndGetNews();
        } finally {
          isLoading.value = false;
        }
      }
      window.addEventListener("click", handleGlobalClick);
    });

    const uniqueSources = computed(() => newsStore.uniqueSources);

    const removeDots = (text) => {
      return text ? text.replace(/\./g, "") : text;
    };

    const filteredNewsList = computed(() => {
      let filtered = [...newsStore.newsList];

      //   
      if (selectedSources.value.length > 0) {
        filtered = filtered.filter(news => 
          selectedSources.value.includes(news.source)
        );
      }

      //   
      if (searchQuery.value) {
        const query = searchQuery.value.toLowerCase();
        filtered = filtered.filter(news => {
          let title = news.title;
          let content = news.content;
          if (news.source.toLowerCase() === "archdaily") {
            title = removeDots(news.title);
            content = removeDots(news.content);
          }
          return title.toLowerCase().includes(query) ||
                 content.toLowerCase().includes(query);
        });
      }

      //  
      if (showFavorites.value) {
        filtered = filtered.filter(news => newsStore.isNewsFavorite(news));
      }

      //    Archdaily
      return filtered.map(news => {
        if (news.source.toLowerCase() === "archdaily") {
          return {
            ...news,
            title: removeDots(news.title),
            content: removeDots(news.content)
          };
        }
        return news;
      });
    });

    const handleGlobalClick = () => {
      isSourcesMenuOpen.value = false;
    };

    const toggleSourcesMenu = (event) => {
      event.stopPropagation();
      isSourcesMenuOpen.value = !isSourcesMenuOpen.value;
    };

    const selectSource = (source) => {
      const index = selectedSources.value.indexOf(source);
      if (index === -1) {
        selectedSources.value.push(source);
      } else {
        selectedSources.value.splice(index, 1);
      }
    };

    const selectAllSources = () => {
      selectedSources.value = [];
    };

    const toggleFavoriteFilter = () => {
      showFavorites.value = !showFavorites.value;
    };

    const isNewsFavorite = (news) => {
      return newsStore.isNewsFavorite(news);
    };

    const toggleFavorite = (news) => {
      newsStore.toggleFavorite(news);
    };

    const formatDate = (date) => {
      return new Date(date).toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long'
      });
    };

    const getImageUrl = (news) => {
      if (news.source.toLowerCase() === "dzen") {
        return require("@/assets/dzen.png");
      }
      if (news.source.toLowerCase() === "archdaily") {
        if (news.image_url) {
          return encodeURI(news.image_url);
        }
        return require("@/assets/dzen.png"); //    
      }
      return news.image;
    };

    return {
      searchQuery,
      isSourcesMenuOpen,
      selectedSources,
      showFavorites,
      isLoading,
      uniqueSources,
      filteredNewsList,
      toggleSourcesMenu,
      selectSource,
      selectAllSources,
      toggleFavoriteFilter,
      isNewsFavorite,
      toggleFavorite,
      formatDate,
      getImageUrl,
      removeDots
    };
  }
};
</script>

<style scoped>
.mobile-news {
  padding: 16px;
  background-color: #e2f3ff;
  min-height: 100vh;
}

.search-section {
  display: flex;
  gap: 12px;
  margin-bottom: 16px;
}

.search-box {
  flex: 1;
  display: flex;
  align-items: center;
  background: white;
  border-radius: 8px;
  padding: 8px 12px;
}

.search-icon {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  margin-right: 8px;
}

.search-input {
  border: none;
  outline: none;
  width: 100%;
  font-size: 14px;
}

.filters {
  display: flex;
  gap: 8px;
}

.filter-btn {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 8px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.filter-btn img {
  width: 20px;
  height: 20px;
  opacity: 0.5;
}

.filter-btn.active {
  background: #539bff;
}

.filter-btn.active img {
  opacity: 1;
  filter: brightness(0) invert(1);
}

.sources-menu {
  background: white;
  border-radius: 8px;
  margin-bottom: 16px;
  overflow: hidden;
}

.source-item {
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
  font-size: 14px;
  cursor: pointer;
}

.source-item:last-child {
  border-bottom: none;
}

.source-item.active {
  color: #539bff;
}

.news-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 16px;
}

.news-image {
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;
}

.news-content {
  padding: 16px;
}

.news-header {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}

.news-title {
  flex: 1;
  margin: 0;
  font-size: 16px;
}

.news-title a {
  color: inherit;
  text-decoration: none;
}

.favorite-btn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
  background: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.favorite-btn img {
  width: 16px;
  height: 16px;
  opacity: 0.5;
}

.favorite-btn.active {
  background: #539bff;
}

.favorite-btn.active img {
  opacity: 1;
  filter: brightness(0) invert(1);
}

.news-description {
  font-size: 14px;
  color: #666;
  margin: 0 0 12px;
  line-height: 1.4;
}

.news-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  color: #666;
}

.news-date {
  display: flex;
  align-items: center;
  gap: 4px;
}

.news-date img {
  width: 14px;
  height: 14px;
  opacity: 0.5;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 48px 0;
  color: #666;
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #eee;
  border-top: 3px solid #539bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

.empty-state {
  text-align: center;
  padding: 48px 0;
  color: #666;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}
</style> 