<template>
  <div>
    <MobileFinance v-if="isMobile" />
    <div v-else class="main-page">
      <h2> </h2>
      <Wallet />
      <h2> </h2>
      <Transactions />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import Wallet from "../Finance/Wallet.vue";
import Transactions from "../Finance/Transactions.vue";
import MobileFinance from "../Finance/MobileFinance.vue";

export default {
  components: {
    Wallet,
    Transactions,
    MobileFinance
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  padding: 20px;
  background-color: #e2f3ff;
  border-radius: 10px;
  padding: 20px 250px;
}

@media (max-width: 768px) {
  .main-page {
    margin-left: 0;
    padding: 16px;
  }
}
</style>
