<template>
  <div class="mobile-auction">
    <div v-if="allProjects && allProjects.length > 0" class="projects-list">
      <div
        v-for="project in allProjects"
        :key="project.uuid"
        class="project-card"
      >
        <div class="project-header">
          <h3>{{ project.name }}</h3>
          <span class="category">{{ project.complexity.name }}</span>
        </div>

        <div class="project-info">
          <p class="subtitle">{{ project.subtitle }}</p>
          <div class="meta">
            <span class="duration">
              <img src="@/icons/calendar.svg" alt="Duration" />
              {{ project.estimated_duration }} .
            </span>
            <span class="client">{{ project.client_name }}</span>
          </div>
        </div>

        <div class="project-budget">
          <div v-for="budget in filteredBudgets(project.specialty_budgets)" 
               :key="budget.specialty" 
               class="budget-item">
            <span class="specialty">{{ getSpecialtyName(budget.specialty) }}</span>
            <span class="amount">{{ formatBudget(budget.budget) }} </span>
          </div>
        </div>

        <button 
          class="apply-button" 
          @click="selectProject(project.uuid)"
        >
           
        </button>
      </div>
    </div>
    <div v-else class="no-projects">
        
    </div>

    <!-- Modal Window -->
    <div v-if="showModal" class="modal-overlay">
      <div class="modal-content">
        <h3>{{ errorMessage ? "" : " " }}</h3>
        <p>{{ errorMessage || " ,       ?" }}</p>
        <div class="modal-buttons">
          <button
            v-if="!errorMessage"
            class="confirm-button"
            @click="submitResponse"
            :disabled="isLoading"
          >
            {{ isLoading ? "..." : "" }}
          </button>
          <button class="cancel-button" @click="closeModal">
            {{ errorMessage ? "" : "" }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useProfileStore } from "@/store/profileStore";
import { useUserStore } from "@/store/user";
import { useProjectStore } from "@/store/projects";
import axios from "axios";

export default {
  name: 'MobileAuction',
  setup() {
    const profileStore = useProfileStore();
    const userStore = useUserStore();
    const projectStore = useProjectStore();

    return {
      profileStore,
      userStore,
      projectStore,
    };
  },
  data() {
    return {
      showModal: false,
      selectedProjectUuid: null,
      errorMessage: null,
      isLoading: false,
    };
  },
  computed: {
    allProjects() {
      return this.projectStore.getProjects.filter(project => project.status === "auction");
    },
    isGip() {
      return this.userStore.isGip;
    },
    userSpecialty() {
      return this.profileStore.profileData.physical_profile?.specialty;
    }
  },
  methods: {
    getSpecialtyName(specialtyId) {
      const specialtyMap = {
        1: '',
        2: '-',
        3: ' ',
        4: ' ',
        5: '  ',
        6: '  ',
        7: ' '
      };
      return specialtyMap[specialtyId] || ` ${specialtyId}`;
    },
    formatBudget(budget) {
      return Number(budget).toLocaleString('ru-RU');
    },
    filteredBudgets(budgets) {
      if (this.isGip) {
        return budgets;
      } else {
        return budgets.filter(budget => budget.specialty === this.userSpecialty);
      }
    },
    selectProject(uuid) {
      this.selectedProjectUuid = uuid;
      if (!localStorage.getItem("accessToken")) {
        this.$router.push("/login");
        return;
      }
      this.showModal = true;
    },
    async submitResponse() {
      this.isLoading = true;
      try {
        const accessToken = localStorage.getItem("accessToken");
        if (!accessToken) {
          throw new Error(" ");
        }

        await axios.post(
          `api/projects/responses/?project_uuid=${this.selectedProjectUuid}`,
          {
            message: "    ",
          },
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
              "Content-Type": "application/json",
            },
          }
        );

        this.closeModal();
      } catch (error) {
        let errorMessage = "    ";
        if (error.response?.data?.detail) {
          errorMessage = error.response.data.detail;
        } else if (error.response?.data?.error) {
          errorMessage = error.response.data.error;
        }
        this.errorMessage = errorMessage;
      } finally {
        this.isLoading = false;
      }
    },
    closeModal() {
      this.showModal = false;
      this.errorMessage = null;
    }
  },
  async created() {
    await this.projectStore.fetchProjects();
  }
};
</script>

<style scoped>
.mobile-auction {
  padding: 15px;
  min-height: 100%;
  background: #fff;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.project-card {
  background: white;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 10px;
}

.project-header h3 {
  margin: 0;
  font-size: 18px;
  color: #2c3e50;
}

.category {
  background: #539bff;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.project-info {
  margin-bottom: 15px;
}

.subtitle {
  color: #666;
  font-size: 14px;
  margin: 0 0 10px 0;
}

.meta {
  display: flex;
  gap: 15px;
  font-size: 13px;
  color: #666;
}

.duration {
  display: flex;
  align-items: center;
  gap: 4px;
}

.duration img {
  width: 14px;
  height: 14px;
}

.project-budget {
  border-top: 1px solid #eee;
  padding-top: 10px;
  margin-bottom: 15px;
}

.budget-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
}

.specialty {
  font-size: 14px;
  color: #666;
}

.amount {
  font-weight: 600;
  color: #2c3e50;
}

.apply-button {
  width: 100%;
  background: #539bff;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 500;
}

.no-projects {
  text-align: center;
  padding: 40px 0;
  color: #666;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 12px;
  width: 90%;
  max-width: 320px;
}

.modal-content h3 {
  margin: 0 0 15px 0;
  font-size: 18px;
}

.modal-buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.confirm-button, .cancel-button {
  flex: 1;
  padding: 10px;
  border-radius: 6px;
  border: none;
  font-size: 14px;
}

.confirm-button {
  background: #539bff;
  color: white;
}

.cancel-button {
  background: #f5f5f5;
  color: #666;
}
</style> 