<template>
  <aside
    :class="['sidebar', { collapsed: isSidebarCollapsed }]"
    @mouseenter="isSidebarCollapsed = false"
    @mouseleave="isSidebarCollapsed = true"
  >
    <div class="sidebar-header">
      <h2>{{ isSidebarCollapsed ? "PM" : "PROJECT MANAGER" }}</h2>
    </div>
    <nav class="menu">
      <ul>
        <li>
          <router-link to="/dashboard">
            <img src="@/icons/home.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"> </span>
          </router-link>
        </li>
      </ul>
      <div class="menu-title-container">
        <p v-if="!isSidebarCollapsed" class="menu-title"></p>
        <div v-else class="menu-line"></div>
      </div>
      <ul>
        <li v-if="isCreateProjectVisible">
          <router-link to="/projects">
            <img src="@/icons/plus.svg" alt=" " class="icon" />
            <span v-if="!isSidebarCollapsed"> </span>
          </router-link>
        </li>
        <li>
          <router-link to="/auction">
            <img src="@/icons/auction.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/chats">
            <img src="@/icons/chats.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/finance">
            <img src="@/icons/wallet.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/investments">
            <img src="@/icons/folder.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/calendar">
            <img src="@/icons/calendar.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
      </ul>
      <div class="menu-title-container">
        <p v-if="!isSidebarCollapsed" class="menu-title"></p>
        <div v-else class="menu-line"></div>
      </div>
      <ul>
        <li>
          <router-link to="/shop">
            <img src="@/icons/bag.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/news">
            <img src="@/icons/news.svg" alt="" class="icon" />
            <span v-if="!isSidebarCollapsed"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/documents">
            <img
              src="@/icons/paper.svg"
              alt=" "
              class="icon"
            />
            <span v-if="!isSidebarCollapsed"> </span>
          </router-link>
        </li>
      </ul>
    </nav>
  </aside>
</template>

<script>
import { mapState } from "pinia";
import { useProfileStore } from "@/store/profileStore";

export default {
  name: "Sidebar",
  data() {
    return {
      isSidebarCollapsed: localStorage.getItem("isSidebarCollapsed") === "true",
    };
  },
  computed: {
    ...mapState(useProfileStore, ["profileData"]),
    isCreateProjectVisible() {
      return this.profileData?.physical_profile?.is_gip === true;
    },
  },
  watch: {
    isSidebarCollapsed(newValue) {
      localStorage.setItem("isSidebarCollapsed", newValue);
    },
  },
  async created() {
    this.isSidebarCollapsed = localStorage.getItem("isSidebarCollapsed") === "true";

    //     
    const profileStore = useProfileStore();
    const accessToken = localStorage.getItem("accessToken");

    if (accessToken) {
      profileStore.accessToken = accessToken;
      profileStore.setupAxios();
      await profileStore.fetchUserProfile();
    }
  },
};
</script>

<style scoped>
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 200px;
  background-color: white;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  z-index: 1001;
  transition: width 0.3s ease;
  overflow: hidden;
}

.sidebar.collapsed {
  width: 60px;
}

.sidebar-header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
  height: 40px;
}

.sidebar-header h2 {
  margin: 0;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: opacity 0.3s ease;
}

.sidebar.collapsed .sidebar-header .full-title {
  display: none;
}

.sidebar.collapsed .sidebar-header .short-title {
  display: inline;
}

.sidebar-header .full-title {
  display: inline;
}

.menu-title-container {
  margin: 15px 0;
  display: flex;
  align-items: center;
}

.menu-title {
  color: #666;
  font-size: 14px;
  margin: 0;
}

.menu-line {
  height: 1px;
  background-color: #cccccc;
  width: 30px;
  margin: 10px auto;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu ul li {
  margin-bottom: 15px;
  position: relative;
}

.menu ul li a {
  color: #333;
  text-decoration: none;
  font-size: 14px;
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 10px;
  transition:
    background-color 0.3s,
    transform 0.3s;
}

.menu ul li a span {
  opacity: 1;
  width: auto;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  margin-left: 8px;
  transition:
    opacity 0.3s ease,
    width 0.3s ease;
}

.menu ul li a:hover {
  background-color: #539bff;
  color: white;
  transform: scale(1.02);
  border-radius: 10px;
}

.menu ul li a:hover .icon {
  filter: brightness(0) invert(1);
}

.menu ul li a.router-link-active {
  background-color: #539bff;
  color: white;
  border-radius: 10px;
  transform: scale(1.02);
}

.menu ul li a.router-link-active .icon {
  filter: brightness(0) invert(1);
}

.sidebar.collapsed .menu ul li a {
  justify-content: center;
}

.menu ul li a:hover .icon {
  transform: scale(1.1);
}
</style>
