<template>
  <div>
    <MobileAuction v-if="isMobile" />
    <div v-else class="main-page">
      <h2></h2>
      <ContentAuction />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import ContentAuction from "../Auction/AuctionContent.vue";
import MobileAuction from "../Auction/MobileAuction.vue";

export default {
  components: {
    ContentAuction,
    MobileAuction
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  background-color: #e2f3ff;
  padding: 20px 300px;
}

@media (max-width: 768px) {
  .main-page {
    margin-left: 0;
    padding: 15px;
  }
}
</style>
