<template>
  <div class="main-page">
    <MobileProjects v-if="isMobile" />
    <Projects v-else />
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import Projects from "@/components/Projects/Projects.vue";
import MobileProjects from "@/components/Projects/MobileProjects.vue";

export default {
  components: {
    Projects,
    MobileProjects,
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  background-color: #e2f3ff;
  padding: 20px 300px;
}

@media (max-width: 768px) {
  .main-page {
    margin-left: 0;
    padding: 0;
  }
}
</style>
