<template>
  <div class="mobile-inspector">
    <div class="header">
      <h3></h3>
    </div>
    <div class="logs-container">
      <div v-if="logs.length === 0" class="empty-state">
        <img src="@/icons/bag.svg" alt="No logs" class="empty-icon" />
        <p>   </p>
      </div>
      <div v-else v-for="log in logs" :key="log.id" class="log-item">
        <div class="log-header">
          <span class="log-date">{{ formatDate(log.created_at) }}</span>
        </div>
        <p class="log-description">{{ log.description }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MobileInspector',
  data() {
    return {
      logs: []
    }
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long'
      })
    }
  }
}
</script>

<style scoped>
.mobile-inspector {
  background: white;
  border-radius: 12px;
  padding: 16px;
}

.header {
  margin-bottom: 16px;
}

.header h3 {
  margin: 0;
  font-size: 18px;
  color: #333;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: #666;
}

.empty-icon {
  width: 48px;
  height: 48px;
  opacity: 0.5;
  margin-bottom: 16px;
}

.empty-state p {
  font-size: 16px;
  margin: 0;
}

.log-item {
  padding: 12px;
  background: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 8px;
}

.log-header {
  margin-bottom: 8px;
}

.log-date {
  font-size: 12px;
  color: #666;
}

.log-description {
  font-size: 14px;
  color: #333;
  margin: 0;
}
</style> 