// src/stores/user.js
import { defineStore } from "pinia";
import axios from "axios";
import { useRouter } from "vue-router";

export const useUserStore = defineStore("user", {
  state: () => ({
    isDropdownOpen: false,
    showNotifications: false,
    searchQuery: "",
    profileData: null,
    profileType: null,
    accessToken: "",
    specialties: [],
    legalEntityTypes: [],
    isProfileLoaded: false,
    isSpecialtiesLoaded: false,
    isLegalEntityTypesLoaded: false,
    cachedData: {
      specialties: null,
      legalEntityTypes: null,
      profile: null,
      cacheTimestamp: null,
    },
  }),
  getters: {
    isDataLoaded(state) {
      return (
        state.isProfileLoaded &&
        state.isSpecialtiesLoaded &&
        state.isLegalEntityTypesLoaded
      );
    },
    firstName(state) {
      if (state.profileType === "physical") {
        return state.profileData?.physical_profile?.first_name || "";
      }
      return "";
    },
    fullName(state) {
      if (state.profileType === "physical") {
        const firstName = state.firstName;
        const lastName = state.profileData?.physical_profile?.last_name || "";
        return `${firstName} ${lastName}`.trim() || "";
      } else if (state.profileType === "legal") {
        return (
          state.profileData?.legal_profile?.company_name ||
          " "
        );
      }
      return "";
    },
    displayRole(state) {
      if (!state.profileData) return "...";

      if (state.profileType === "legal") {
        return this.getLegalEntityType();
      }
      return this.getSpecialtyName();
    },
    userAvatar(state) {
      if (state.profileType === "legal") {
        return (
          state.profileData?.legal_profile?.photo_company ||
          require("@/assets/avatar.png")
        );
      }
      return (
        state.profileData?.physical_profile?.photo ||
        require("@/assets/avatar.png")
      );
    },
    isGip: (state) => state.profileData?.physical_profile?.is_gip === true,
  },
  actions: {
    isCacheValid() {
      const now = Date.now();
      const cacheAge = now - (this.cachedData.cacheTimestamp || 0);
      return cacheAge < 24 * 60 * 60 * 1000; // 24   
    },
    async initialize() {
      this.accessToken = localStorage.getItem("accessToken");
      
      if (!this.accessToken) {
        return false;
      }

      try {
        const cachedData = localStorage.getItem('userStoreCache');
        if (cachedData) {
          const parsedCache = JSON.parse(cachedData);
          if (parsedCache.cacheTimestamp && this.isCacheValid()) {
            this.cachedData = parsedCache;
            this.profileData = parsedCache.profile;
            this.profileType = parsedCache.profile?.profile_type;
            this.specialties = parsedCache.specialties;
            this.legalEntityTypes = parsedCache.legalEntityTypes;
            this.isProfileLoaded = true;
            this.isSpecialtiesLoaded = true;
            this.isLegalEntityTypesLoaded = true;
            return true;
          }
        }

        const userProfile = await this.fetchUserProfile();
        localStorage.setItem("user_id", userProfile.id);
        await Promise.all([
          this.fetchSpecialties(),
          this.fetchLegalEntityTypes(),
        ]);

        this.cachedData = {
          profile: this.profileData,
          specialties: this.specialties,
          legalEntityTypes: this.legalEntityTypes,
          cacheTimestamp: Date.now(),
        };
        localStorage.setItem('userStoreCache', JSON.stringify(this.cachedData));
        
        return true;
      } catch (error) {
        localStorage.removeItem("accessToken");
        localStorage.removeItem("refreshToken");
        localStorage.removeItem("userStoreCache");
        return false;
      }
    },
    async fetchUserProfile() {
      try {
        const response = await axios.get(
          "api/accounts/profile/",
          {
            headers: {
              Authorization: `Bearer ${this.accessToken}`,
            },
          },
        );
        this.profileData = response.data;
        this.profileType = response.data.profile_type;
        this.isProfileLoaded = true;
        return response.data;
      } catch (error) {
        if (error.response?.status === 401) {
          this.accessToken = null;
          localStorage.removeItem("accessToken");
        }
        throw error;
      }
    },
    async fetchSpecialties() {
      try {
        const response = await axios.get(
          "api/accounts/specialties/",
          {
            headers: {
              Authorization: `Bearer ${this.accessToken}`,
            },
          },
        );
        this.specialties = Array.isArray(response.data)
          ? response.data
          : response.data.results || [];
        this.isSpecialtiesLoaded = true;
      } catch (error) {
        throw error;
      }
    },
    async fetchLegalEntityTypes() {
      try {
        const response = await axios.get(
          "api/accounts/legal-entity-types/",
          {
            headers: {
              Authorization: `Bearer ${this.accessToken}`,
            },
          },
        );
        this.legalEntityTypes = Array.isArray(response.data)
          ? response.data
          : response.data.results || [];
        this.isLegalEntityTypesLoaded = true;
      } catch (error) {
        throw error;
      }
    },
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    toggleNotifications() {
      this.showNotifications = !this.showNotifications;
    },
    performSearch() {
      // Handle search action
    },
    logout(router) {
      localStorage.removeItem("accessToken");
      localStorage.removeItem("refreshToken");
      localStorage.removeItem("profileComplete");
      localStorage.removeItem("profileType");
      localStorage.removeItem("userStoreCache");
      router.push("/landing");
    },
    getLegalEntityType() {
      const typeId = this.profileData?.legal_profile?.legal_entity_type;
      if (!typeId) return " ";

      const type = this.legalEntityTypes.find((item) => item.id === typeId);
      if (!type) return " ";

      const typeMap = {
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: "  ",
      };

      return typeMap[type.id] || type.name;
    },
    getSpecialtyName() {
      if (this.profileData?.physical_profile?.is_gip === true) {
        return "";
      }

      const specialtyId = this.profileData?.physical_profile?.specialty;
      if (!specialtyId || !Array.isArray(this.specialties)) return " ";

      const specialty = this.specialties.find((s) => s.id === specialtyId);
      return specialty ? specialty.name : " ";
    },
  },
});
