import { defineStore } from 'pinia'
import axios from 'axios'

export const useResponsesStore = defineStore('responses', {
  state: () => ({
    responses: [],
    isLoading: false,
    error: null,
    showNotification: false,
    notificationMessage: '',
  }),

  getters: {
    pendingResponses: (state) => 
      state.responses.filter((response) => response.status === 'pending'),
    
    acceptedResponses: (state) =>
      state.responses.filter((response) => response.status === 'accepted'),
    
    rejectedResponses: (state) =>
      state.responses.filter((response) => response.status === 'rejected'),
  },

  actions: {
    async fetchResponses() {
      this.isLoading = true
      this.error = null
      try {
        const response = await axios.get(
          'api/projects/responses/',
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
            },
          }
        )
        this.responses = response.data
      } catch (error) {
        console.error('   :', error)
        this.error = '    '
      } finally {
        this.isLoading = false
      }
    },

    async acceptResponse(uuid) {
      try {
        await axios.post(
          `api/projects/responses/${uuid}/accept/`,
          {},
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
            },
          }
        )
        await this.fetchResponses()
        this.showSuccessNotification('  ')
      } catch (error) {
        console.error('   :', error)
        this.error = error.message || '    '
      }
    },

    async rejectResponse(uuid) {
      try {
        await axios.post(
          `api/projects/${uuid}/reject/`,
          {},
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
            },
          }
        )
        await this.fetchResponses()
        this.showSuccessNotification('  ')
      } catch (error) {
        console.error('   :', error)
        this.error = error.message || '    '
      }
    },

    async moveToAuction(projectUuid) {
      this.isLoading = true;
      try {
        await axios.post(
          `api/projects/${projectUuid}/send_to_auction/`,
          {},
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
            },
          }
        );
        this.showSuccessNotification('    ');
      } catch (error) {
        console.error('   :', error);
        throw error;
      } finally {
        this.isLoading = false;
      }
    },

    async moveToWork(projectUuid) {
      this.isLoading = true;
      try {
        await axios.post(
          `api/projects/${projectUuid}/start_project/`,
          {},
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
            },
          }
        );
        this.showSuccessNotification('    ');
      } catch (error) {
        console.error('     :', error);
        throw error;
      } finally {
        this.isLoading = false;
      }
    },

    showSuccessNotification(message) {
      this.notificationMessage = message
      this.showNotification = true
      setTimeout(() => {
        this.showNotification = false
      }, 3000)
    },
  },
}) 