<template>
  <div class="mobile-dashboard">
    <!--  -->
    <div class="welcome-section">
      <h2> , {{ userStore.firstName }}</h2>
    </div>

    <!--   -->
    <div class="mobile-banner">
      <h1>PROJECT MANAGER</h1>
      <div class="banner-line"></div>
      <p>-</p>
    </div>

    <!--   -->
    <div class="projects-section">
      <div class="section-header">
        <h3> </h3>
        <button class="view-all"></button>
      </div>
      
      <div v-if="projects.length === 0" class="no-projects">
        <img src="@/icons/folder.svg" alt="No projects" />
        <p>    </p>
      </div>
      
      <div v-else class="projects-list">
        <div v-for="project in projects" :key="project.id" class="project-card">
          <div class="project-header">
            <img :src="project.avatar" alt="Project" class="project-avatar" />
            <div class="project-info">
              <h4>{{ project.name }}</h4>
              <span class="owner">{{ project.owner }}</span>
            </div>
          </div>
          
          <div class="project-details">
            <div class="detail-item">
              <span class="label"></span>
              <div class="progress-bar">
                <div 
                  class="progress-fill"
                  :style="{ width: `${project.progress}%` }"
                ></div>
              </div>
            </div>
            
            <div class="detail-row">
              <div class="detail-item">
                <span class="label"></span>
                <span :class="['priority-badge', project.priorityClass]">
                  {{ project.priority }}
                </span>
              </div>
              <div class="detail-item">
                <span class="label"></span>
                <span class="budget">{{ formatBudget(project.budget) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="news-section">
      <div class="section-header">
        <h3></h3>
        <button class="view-all" @click="goToNews"></button>
      </div>

      <div v-if="latestNews.length === 0" class="no-news">
        <img src="@/icons/news.svg" alt="No news" />
        <p>  </p>
      </div>

      <div v-else class="news-list">
        <div 
          v-for="news in latestNews" 
          :key="news.id" 
          class="news-card"
          @click="goToNews"
        >
          <div 
            class="news-image" 
            :style="{ backgroundImage: `url(${getImageUrl(news)})` }"
          ></div>
          <div class="news-content">
            <h4>{{ removeDots(news.title) }}</h4>
            <div class="news-date">
              <img src="@/icons/calendar.svg" alt="Date" />
              <span>{{ formatDate(news.published_date) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useUserStore } from '@/store/user'
import { useRouter } from 'vue-router'
import axios from 'axios'

export default {
  name: 'MobileDashboard',
  setup() {
    const userStore = useUserStore()
    const router = useRouter()
    return { userStore, router }
  },
  data() {
    return {
      projects: [],
      latestNews: [],
      defaultImage: require('@/assets/dzen.png')
    }
  },
  methods: {
    formatBudget(amount) {
      return new Intl.NumberFormat('ru-RU', {
        style: 'currency',
        currency: 'KZT',
        minimumFractionDigits: 0
      }).format(amount)
    },
    removeDots(text) {
      return text ? text.replace(/\./g, '') : text
    },
    formatDate(dateString) {
      const options = { day: 'numeric', month: 'long' }
      return new Date(dateString).toLocaleDateString('ru-RU', options)
    },
    getImageUrl(news) {
      return news.image_url ? encodeURI(news.image_url) : this.defaultImage
    },
    goToNews() {
      this.router.push('/news')
    },
    async fetchNews() {
      try {
        const response = await axios.get('api/news/')
        this.latestNews = response.data
          .filter(news => news.source === 'Archdaily')
          .sort((a, b) => new Date(b.published_date) - new Date(a.published_date))
          .slice(0, 3)
      } catch (error) {
        console.error('Error fetching news:', error)
      }
    }
  },
  mounted() {
    this.fetchNews()
  }
}
</script>

<style scoped>
.mobile-dashboard {
  padding: 15px;
}

.welcome-section {
  margin-bottom: 20px;
}

.welcome-section h2 {
  font-size: 20px;
  color: #333;
  margin: 0;
}

.mobile-banner {
  background: linear-gradient(135deg, #559cff 0%, #20549c 100%);
  padding: 20px;
  border-radius: 12px;
  color: white;
  text-align: center;
  margin-bottom: 20px;
}

.mobile-banner h1 {
  font-size: 24px;
  margin: 0;
}

.banner-line {
  width: 60px;
  height: 2px;
  background: rgba(255, 255, 255, 0.5);
  margin: 10px auto;
}

.mobile-banner p {
  margin: 0;
  font-size: 14px;
  opacity: 0.8;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.section-header h3 {
  font-size: 18px;
  margin: 0;
}

.view-all {
  background: none;
  border: none;
  color: #539bff;
  font-size: 14px;
  padding: 5px;
}

.projects-section,
.news-section {
  background: white;
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 20px;
}

.no-projects,
.no-news {
  text-align: center;
  padding: 30px 0;
  color: #666;
}

.no-projects img,
.no-news img {
  width: 40px;
  height: 40px;
  opacity: 0.5;
  margin-bottom: 10px;
}

.project-card {
  background: #f8f9fa;
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 15px;
}

.project-header {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.project-avatar {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  margin-right: 12px;
}

.project-info h4 {
  margin: 0;
  font-size: 16px;
}

.owner {
  font-size: 12px;
  color: #666;
}

.progress-bar {
  height: 6px;
  background: #e9ecef;
  border-radius: 3px;
  overflow: hidden;
  margin-top: 5px;
}

.progress-fill {
  height: 100%;
  background: #539bff;
  border-radius: 3px;
  transition: width 0.3s ease;
}

.detail-row {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.detail-item {
  margin-top: 10px;
}

.label {
  font-size: 12px;
  color: #666;
  display: block;
  margin-bottom: 5px;
}

.priority-badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  color: white;
}

.priority-badge.high {
  background: rgba(229, 57, 53, 0.6);
}

.priority-badge.medium {
  background: rgba(251, 192, 45, 0.6);
}

.priority-badge.low {
  background: rgba(67, 160, 71, 0.6);
}

.budget {
  font-size: 14px;
  font-weight: 500;
}

.news-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.news-card {
  display: flex;
  background: #f8f9fa;
  border-radius: 10px;
  overflow: hidden;
}

.news-image {
  width: 100px;
  height: 100px;
  background-size: cover;
  background-position: center;
}

.news-content {
  flex: 1;
  padding: 12px;
  background-color: white;
}

.news-content h4 {
  margin: 0 0 8px 0;
  font-size: 14px;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  overflow: hidden;
}

.news-date {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 12px;
  color: #666;
}

.news-date img {
  width: 14px;
  height: 14px;
  opacity: 0.7;
}
</style> 