<template>
  <div v-if="show" class="notifications-sidebar">
    <div class="notifications-header">
      <h3></h3>
      <button class="close-button" @click="$emit('close')">
        <img src="@/icons/cross.svg" alt="" />
      </button>
    </div>
    <div class="notifications-list">
      <div class="empty-notifications" v-if="unreadMessages.length === 0">
        <img src="@/icons/notification.svg" alt=" " />
        <p>  </p>
      </div>
      <div v-else>
        <p>  {{ unreadMessages.length }}  </p>
        <!--        -->
        <div class="scroll-container">
          <ul class="messages-list">
            <li
              v-for="(msg, index) in unreadMessages"
              :key="index"
              class="message-item"
            >
              <div class="message-text">{{ msg.message }}</div>
              <div class="message-meta">
                <span class="timestamp">{{
                  formatTimestamp(msg.timestamp)
                }}</span>
                <span class="chat-id"> #{{ msg.chat_id }}</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useUserStore } from "@/store/user";

export default {
  name: "Notifications",
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["close"],
  data() {
    return {
      unreadMessages: [],
      intervalId: null,
      userStore: useUserStore(),
    };
  },
  methods: {
    async fetchUnreadCount() {
      try {
        const token = localStorage.getItem("accessToken");
        const response = await axios.get(
          "api/chat/unread/",
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          },
        );
        this.unreadMessages = response.data.unread_messages || [];
        this.userStore.unreadCount = this.unreadMessages.length;
      } catch (error) {
        // Handle error fetching unread count
      }
    },
    formatTimestamp(timestamp) {
      try {
        const date = new Date(timestamp);
        return date.toLocaleString("ru-RU", {
          year: "numeric",
          month: "long",
          day: "numeric",
          hour: "2-digit",
          minute: "2-digit",
        });
      } catch (e) {
        return timestamp;
      }
    },
  },
  mounted() {
    //  
    this.fetchUnreadCount();

    //    30 
    this.intervalId = setInterval(() => {
      this.fetchUnreadCount();
    }, 30000);
  },
  beforeUnmount() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  },
};
</script>

<style scoped>
.notifications-sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 320px;
  height: 100vh;
  background: white;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s ease;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

.notifications-header {
  padding: 10px;
  border-bottom: 1px solid #eaeaea;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notifications-header h3 {
  margin: 10px;
  font-size: 18px;
  font-weight: 600;
  color: #333;
  text-transform: none;
  letter-spacing: normal;
}

.close-button {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 6px;
  transition: background-color 0.2s;
}

.close-button img {
  width: 24px;
  height: 24px;
  opacity: 0.7;
  transition: filter 0.2s;
}

.close-button:hover {
  background-color: #539bff;
}

.close-button:hover img {
  filter: brightness(0) invert(1);
  opacity: 1;
}

.notifications-list {
  flex: 1;
  padding: 20px;
}

.empty-notifications {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #999;
}

.empty-notifications img {
  width: 48px;
  height: 48px;
  opacity: 0.5;
  margin-bottom: 15px;
}

.empty-notifications p {
  font-size: 16px;
  margin: 0;
}

.messages-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.message-item {
  background: #f7f9fc;
  margin-bottom: 10px;
  padding: 10px 15px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}

.message-text {
  font-size: 14px;
  color: #333;
  margin-bottom: 5px;
}

.message-meta {
  font-size: 12px;
  color: #999;
  display: flex;
  justify-content: space-between;
}

/*   ,    */
.scroll-container {
  max-height: 800px;
  overflow-y: auto;
}

/*    (,   ) */
.scroll-container::-webkit-scrollbar {
  width: 0; /*    Chrome/Safari/Opera */
}
.scroll-container {
  -ms-overflow-style: none; /* IE  Edge */
  scrollbar-width: none; /* Firefox */
}
</style>
