<template>
  <div class="mobile-profile">
    <!--     -->
    <div class="profile-header">
      <div class="photo-container">
        <div class="photo-circle">
          <img
            v-if="profileStore.profileData.physical_profile?.photo"
            :src="profileStore.profileData.physical_profile.photo"
            alt=" "
          />
          <span v-else> </span>
        </div>
        <div class="user-info">
          <h2>{{ fullName }}</h2>
          <p class="specialty">{{ profileStore.getSpecialtyName || "  " }}</p>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="stats-container">
      <div class="stat-item">
        <div class="stat-value">45<span></span></div>
        <div class="stat-label"> </div>
      </div>
      <div class="stat-item">
        <div class="stat-value">3</div>
        <div class="stat-label">  </div>
      </div>
      <div class="stat-item">
        <div class="stat-value">11</div>
        <div class="stat-label"> </div>
      </div>
    </div>

    <!--   -->
    <div class="info-section">
      <h3> </h3>
      <div class="info-list">
        <div class="info-item">
          <span class="label">Email</span>
          <span class="value">{{ profileStore.profileData.email || " " }}</span>
        </div>
        <div class="info-item">
          <span class="label"></span>
          <span class="value">{{ profileStore.profileData.physical_profile?.phone || " " }}</span>
        </div>
        <div class="info-item">
          <span class="label"></span>
          <span class="value">{{ profileStore.profileData.physical_profile?.iin || " " }}</span>
        </div>
        <div class="info-item">
          <span class="label"> </span>
          <span class="value">{{ profileStore.getExperienceName }}</span>
        </div>
        <div class="info-item">
          <span class="label"></span>
          <span class="value">{{ profileStore.getEducationName }}</span>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="documents-section" v-if="profileStore.profileData.physical_profile">
      <h3></h3>
      <div class="documents-list">
        <DocumentItem
          v-for="doc in documents"
          :key="doc.fileName"
          :label="doc.label"
          :fileName="doc.fileName"
          :baseDownloadUrl="profileStore.baseDownloadUrl"
          :downloadFile="profileStore.downloadFile"
          :profileData="profileStore.profileData"
          :profileType="profileStore.profileType"
        />
      </div>
    </div>

    <!--   -->
    <div class="logout-section">
      <button class="logout-button" @click="handleLogout">
        <img src="@/icons/logout.svg" alt="" />
        <span></span>
      </button>
    </div>
  </div>
</template>

<script>
import { useProfileStore } from "@/store/profileStore";
import { onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import DocumentItem from "./DocumentItem.vue";

export default {
  components: {
    DocumentItem,
  },
  setup() {
    const profileStore = useProfileStore();
    const router = useRouter();

    onMounted(async () => {
      profileStore.accessToken = localStorage.getItem("accessToken");
      if (!profileStore.accessToken) {
        router.push("/landing");
      } else {
        profileStore.setupAxios();
        await profileStore.fetchUserProfile();
      }
    });

    const fullName = computed(() => {
      const { last_name, first_name, middle_name } =
        profileStore.profileData.physical_profile || {};
      return [last_name, first_name, middle_name].filter(Boolean).join(" ") || " ";
    });

    const documents = [
      { label: "  ", fileName: "scan_of_ID" },
      { label: "   ", fileName: "person_with_ID" },
    ];

    const handleLogout = () => {
      localStorage.removeItem('accessToken')
      router.push('/landing')
    }

    return {
      profileStore,
      fullName,
      documents,
      handleLogout
    };
  },
};
</script>

<style scoped>
.mobile-profile {
  background: #fff;
  min-height: 100%;
}

.profile-header {
  padding: 20px;
  background: #fff;
  border-bottom: 1px solid #eee;
}

.photo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.photo-circle {
  width: 120px;
  height: 120px;
  border-radius: 60px;
  overflow: hidden;
  margin-bottom: 15px;
  border: 3px solid #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.photo-circle img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-info h2 {
  margin: 0;
  font-size: 20px;
  color: #2c3e50;
}

.specialty {
  color: #666;
  font-size: 14px;
  margin: 5px 0 0;
}

.stats-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  padding: 15px;
  background: #fff;
  border-bottom: 1px solid #eee;
}

.stat-item {
  text-align: center;
}

.stat-value {
  font-size: 20px;
  font-weight: 600;
  color: #539bff;
}

.stat-value span {
  font-size: 14px;
  margin-left: 2px;
}

.stat-label {
  font-size: 12px;
  color: #666;
  margin-top: 4px;
}

.info-section, .documents-section {
  padding: 20px;
  background: #fff;
  margin-top: 10px;
}

.info-section h3, .documents-section h3 {
  margin: 0 0 15px;
  font-size: 18px;
  color: #2c3e50;
}

.info-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 12px;
  border-bottom: 1px solid #eee;
}

.info-item .label {
  color: #666;
  font-size: 14px;
}

.info-item .value {
  color: #2c3e50;
  font-size: 14px;
  font-weight: 500;
}

.documents-section {
  padding: 15px;
  background: #fff;
  margin-top: 10px;
}

.documents-section h3 {
  margin: 0 0 12px;
  font-size: 16px;
  color: #2c3e50;
}

.documents-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/*     DocumentItem */
:deep(.document-item) {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background: #f8f9fa;
  border-radius: 8px;
  gap: 10px;
}

:deep(.document-info) {
  flex: 1;
  min-width: 0; /*    text-overflow */
}

:deep(.document-label) {
  font-size: 13px;
  color: #2c3e50;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

:deep(.document-actions) {
  display: flex;
  align-items: center;
  gap: 8px;
}

:deep(.btn-download) {
  padding: 6px 10px;
  font-size: 13px;
  white-space: nowrap;
  background: #539bff;
  color: white;
  border: none;
  border-radius: 6px;
}

.logout-section {
  padding: 20px;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 40px;
}

.logout-button {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  background: #fff;
  border: 1px solid #ff4d4d;
  border-radius: 8px;
  color: #ff4d4d;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-button img {
  width: 20px;
  height: 20px;
  filter: invert(45%) sepia(60%) saturate(2848%) hue-rotate(338deg) brightness(100%) contrast(102%);
}

.logout-button:hover {
  background: #ff4d4d;
  color: white;
}

.logout-button:hover img {
  filter: brightness(0) invert(1);
}
</style> 