<template>
  <div class="mobile-chats">
    <!--   -->
    <div v-if="!chatStore.selectedChat" class="chats-list">
      <div class="tabs">
        <button 
          :class="{ active: chatStore.selectedTab === 'contacts' }"
          @click="chatStore.selectedTab = 'contacts'"
        >
          
        </button>
        <button 
          :class="{ active: chatStore.selectedTab === 'groups' }"
          @click="chatStore.selectedTab = 'groups'"
        >
          
        </button>
      </div>

      <!--  -->
      <div v-if="chatStore.selectedTab === 'contacts'" class="chat-items">
        <div
          v-for="(chat, index) in chatStore.privateChats"
          :key="chat.id"
          class="chat-item"
          @click="selectChat(chat)"
        >
          <div class="chat-info">
            <img
              :src="chat.companion?.photo || chat.participants[0]?.user?.photo || require('@/assets/avatar.png')"
              alt="Chat Photo"
              class="chat-avatar"
            />
            <div class="chat-text">
              <h4>{{ getParticipantName(chat) }}</h4>
              <p v-if="chat.last_message">
                {{ chat.last_message.content }}
                <span class="message-time">
                  {{ chatStore.formatTime(chat.last_message.created_at) }}
                </span>
              </p>
              <p v-else> </p>
            </div>
          </div>
          <span v-if="chat.unread" class="unread-count">
            {{ chat.unread }}
          </span>
        </div>
      </div>

      <!--  -->
      <div v-else class="chat-items">
        <div
          v-for="chat in chatStore.groupChats"
          :key="chat.id"
          class="chat-item"
          @click="selectChat(chat)"
        >
          <div class="chat-info">
            <img
              :src="chat.photo || require('@/assets/avatar.png')"
              alt="Group Photo"
              class="chat-avatar"
            />
            <div class="chat-text">
              <h4>{{ chat.name }}</h4>
              <p class="participants-count">
                {{ chat.participants?.length || 0 }} 
              </p>
              <p v-if="chat.last_message">
                {{ chat.last_message.content }}
                <span class="message-time">
                  {{ chatStore.formatTime(chat.last_message.created_at) }}
                </span>
              </p>
              <p v-else> </p>
            </div>
          </div>
          <span v-if="chat.unread" class="unread-count">
            {{ chat.unread }}
          </span>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-else class="chat-view">
      <!--   -->
      <div class="chat-header">
        <button class="back-button" @click="closeChat">
          <img src="@/icons/left.svg" alt="Back" />
        </button>
        <div class="chat-info">
          <img
            :src="getCurrentChatPhoto"
            alt="Chat Photo"
            class="chat-avatar"
          />
          <div class="chat-header-text">
            <h4>{{ getCurrentChatName }}</h4>
            <p v-if="!isGroupChat">{{ getCurrentChatEmail }}</p>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="messages" ref="messagesContainer">
        <div
          v-for="group in chatStore.groupMessagesByDate(
            chatStore.getMessagesForCurrentChat
          )"
          :key="group.date"
        >
          <div class="date-header">
            {{ chatStore.formatDateHeader(group.date) }}
          </div>
          <div
            v-for="msg in group.messages"
            :key="msg.id"
            :class="{
              'message-wrapper': true,
              'my-message': msg.is_own,
              'their-message': !msg.is_own,
            }"
          >
            <div class="message-bubble">
              <div 
                v-if="isGroupChat && !msg.is_own" 
                class="sender-name"
              >
                {{ chatStore.getSenderName(msg.sender_id || msg.sender) }}
              </div>
              <div class="message-content">
                <p>{{ msg.message }}</p>
                <span class="message-time">{{ chatStore.formatTime(msg.timestamp || msg.created_at) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--   -->
      <div class="message-input-container">
        <div class="message-input-wrapper">
          <div class="emoji-container" ref="emojiButton">
            <img
              src="@/icons/smile.svg"
              alt="Emoji"
              @click="toggleEmojiPicker"
            />
          </div>
          <input
            type="text"
            placeholder=" "
            v-model="chatStore.message"
            @keyup.enter="chatStore.sendMessage"
          />
          <img
            src="@/icons/vector.svg"
            alt="Attachment"
          />
        </div>
        <button class="send-button" @click="chatStore.sendMessage">
          <img 
            :src="chatStore.message ? require('@/icons/send.svg') : require('@/icons/voice.svg')" 
            :alt="chatStore.message ? 'Send' : 'Voice'" 
          />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import { useChatStore } from "@/store/chatStore";

export default {
  name: 'MobileChats',
  setup() {
    const chatStore = useChatStore();
    const messagesContainer = ref(null);

    onMounted(() => {
      window.scrollTo(0, 0);
      
      const token = localStorage.getItem("accessToken");
      if (token) {
        chatStore.setAccessToken(token);
        const currentUserId = localStorage.getItem("userId");
        chatStore.setCurrentUserId(currentUserId);
        chatStore.fetchChats();
      }
    });

    const getCurrentChatPhoto = computed(() => {
      if (!chatStore.selectedChat) return require('@/assets/avatar.png');
      
      if (chatStore.selectedChat.chat_type === 'group') {
        return chatStore.selectedChat.photo || require('@/assets/avatar.png');
      }
      
      return chatStore.selectedChat.companion?.photo || 
             chatStore.selectedChat.participants[0]?.user?.photo || 
             require('@/assets/avatar.png');
    });

    const getCurrentChatName = computed(() => {
      if (!chatStore.selectedChat) return '';
      
      if (chatStore.selectedChat.chat_type === 'group') {
        return chatStore.selectedChat.name;
      }
      
      return getParticipantName(chatStore.selectedChat);
    });

    const getCurrentChatEmail = computed(() => {
      if (!chatStore.selectedChat || chatStore.selectedChat.chat_type === 'group') return '';
      return chatStore.selectedChat.participants[0].user.email;
    });

    const isGroupChat = computed(() => {
      return chatStore.selectedChat?.chat_type === 'group';
    });

    const getParticipantName = (chat) => {
      if (chat.companion) {
        return chat.companion.full_name;
      }
      
      return chat.participants[0].user.profile_type === "physical"
        ? `${chat.participants[0].user.first_name} ${chat.participants[0].user.last_name}`
        : chat.participants[0].user.company_name;
    };

    const selectChat = (chat) => {
      chatStore.connectToChat(chat);
      window.scrollTo(0, 0);
    };

    const closeChat = () => {
      chatStore.selectedChat = null;
    };

    const toggleEmojiPicker = () => {
      if (!chatStore.picker) {
        chatStore.initEmojiPicker();
      }
      chatStore.emojiButtonRef = document.querySelector('.emoji-container');
      chatStore.toggleEmojiPicker();
    };

    return {
      chatStore,
      messagesContainer,
      getCurrentChatPhoto,
      getCurrentChatName,
      getCurrentChatEmail,
      isGroupChat,
      getParticipantName,
      selectChat,
      closeChat,
      toggleEmojiPicker
    };
  }
};
</script>

<style scoped>
.mobile-chats {
  height: 100%;
  background: #fff;
}

.tabs {
  display: flex;
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #eee;
}

.tabs button {
  flex: 1;
  padding: 10px;
  border: none;
  background: none;
  font-size: 16px;
  color: #666;
}

.tabs button.active {
  color: #539bff;
  border-bottom: 2px solid #539bff;
}

.chat-items {
  overflow-y: auto;
  height: calc(100vh - 130px);
}

.chat-item {
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #eee;
}

.chat-info {
  display: flex;
  flex: 1;
  align-items: center;
  gap: 10px;
}

.chat-avatar {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  object-fit: cover;
}

.chat-text {
  flex: 1;
}

.chat-text h4 {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
}

.chat-text p {
  margin: 2px 0 0;
  color: #666;
  font-size: 12px;
}

.message-time {
  font-size: 12px;
  color: #999;
}

.unread-count {
  background: #539bff;
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 12px;
}

.chat-view {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 1000;
  overflow-y: hidden;
}

.chat-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 8px;
  background: #fff;
  border-bottom: 1px solid #eee;
  min-height: 48px;
  z-index: 10;
}

.back-button {
  background: none;
  border: none;
  padding: 6px;
  margin-right: 8px;
}

.back-button img {
  width: 20px;
  height: 20px;
}

.messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  background: #fff;
  margin-top: 48px;
  margin-bottom: 60px;
  -webkit-overflow-scrolling: touch;
}

.message-wrapper {
  margin-bottom: 10px;
}

.message-bubble {
  max-width: 80%;
  padding: 10px;
  border-radius: 10px;
}

.my-message {
  display: flex;
  justify-content: flex-end;
}

.my-message .message-bubble {
  background: #d9fdd3;
}

.their-message .message-bubble {
  background: #fff;
}

.message-input-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  background: #fff;
  border-top: 1px solid #eee;
  display: flex;
  gap: 10px;
  z-index: 10;
}

.message-input-wrapper {
  flex: 1;
  display: flex;
  align-items: center;
  background: #fff;
  border-radius: 20px;
  padding: 5px 15px;
  border: 1px solid #eee;
}

.message-input-wrapper input {
  flex: 1;
  border: none;
  background: none;
  padding: 10px;
  outline: none;
}

.send-button {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background: #539bff;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.send-button img {
  width: 20px;
  height: 20px;
  filter: brightness(0) invert(1);
}

.date-header {
  text-align: center;
  margin: 20px 0;
  color: #666;
  font-size: 12px;
}

.sender-name {
  font-size: 12px;
  color: #539bff;
  margin-bottom: 4px;
}

.chat-header-text h4 {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
}

.chat-header-text p {
  margin: 2px 0 0;
  color: #666;
  font-size: 12px;
}

.message-content {
  display: flex;
  align-items: flex-end;
  gap: 8px;
}

.message-content p {
  margin: 0;
}

.message-time {
  font-size: 11px;
  color: #999;
  white-space: nowrap;
  margin-bottom: 2px;
}
</style> 