import { defineStore } from "pinia";
import axios from "axios";

export const useNewsStore = defineStore("news", {
  state: () => ({
    newsList: [],
    favoriteNews: [],
  }),
  getters: {
    uniqueSources: (state) => {
      return [...new Set(state.newsList.map((news) => news.source))];
    },
    isNewsFavorite: (state) => {
      return (news) =>
        state.favoriteNews.some((favorite) => favorite.id === news.id);
    },
  },
  actions: {
    async fetchAndGetNews() {
      try {
        //  POST-   
        await axios.post("api/news/fetch/");

        //  GET-   
        const response = await axios.get(
          "api/news/",
        );

        this.newsList = response.data;
      } catch (error) {
        // Handle error fetching news
      }
    },
    toggleFavorite(news) {
      if (this.favoriteNews.some((favorite) => favorite.id === news.id)) {
        this.favoriteNews = this.favoriteNews.filter(
          (favorite) => favorite.id !== news.id,
        );
      } else {
        this.favoriteNews.push(news);
      }
    },
  },
});
