<template>
  <div>
    <MobileInvestments v-if="isMobile" />
    <div v-else class="main-page">
      <h2></h2>
      <div class="horizontal-container">
        <WorkData />
        <Inspector />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import WorkData from "../Investments/WorkData.vue";
import Inspector from "../Investments/Inspector.vue";
import MobileInvestments from "./MobileInvestments.vue";

export default {
  components: {
    WorkData,
    Inspector,
    MobileInvestments
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: calc(100vh - 200px);
  background-color: #e2f3ff;
  padding: 20px 250px;
}
.horizontal-container {
  display: flex;
  flex-direction: row; /*   */
  gap: 16px; /*    */
}
</style>
