<template>
  <div class="create-project-page">
    <MobileCreateProjects v-if="isMobile" @close="goBack" />
    <CreateProjects v-else @close="goBack" />
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { useRouter } from "vue-router";
import CreateProjects from "@/components/Projects/CreateProjects.vue";
import MobileCreateProjects from "@/components/Projects/MobileCreateProjects.vue";

const router = useRouter();
const isMobile = ref(window.innerWidth <= 768);

const checkDevice = () => {
  isMobile.value = window.innerWidth <= 768;
};

onMounted(() => {
  window.addEventListener('resize', checkDevice);
});

onBeforeUnmount(() => {
  window.removeEventListener('resize', checkDevice);
});

const goBack = () => {
  router.push("/projects");
};
</script>

<style scoped>
.create-project-page {
  background-color: #e2f3ff;
  padding: 20px 300px;
}

@media (max-width: 768px) {
  .create-project-page {
    padding: 0;
  }
}
</style>
