}

.member-role {
  font-size: 12px;
  color: #6c757d;
  font-weight: 500;
}

.no-members {
  text-align: center;
  color: #666;
  padding: 10px;
  font-style: italic;
}

.task-form {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  color: #2c3e50;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
