  color: #666;
  margin-bottom: 4px;
}

.task-assignee {
  font-size: 12px;
  color: #1976d2;
  font-weight: 500;
}

.task-status-legend {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 15px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #dddddda1;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #666;
}

.status-dot {
  width: 10px;
  height: 10px;
