}

.task-indicator.red {
  background-color: #ffebee;
  color: #c62828;
  border-left: 3px solid #c62828;
}

.task-title.green {
  color: #2e7d32;
}

.task-title.yellow {
  color: #f57c00;
}

.task-title.red {
  color: #c62828;
}

.status-dot.green {
  background-color: #2e7d32;
}

.status-dot.yellow {
  background-color: #f57c00;
}

.status-dot.red {
  background-color: #c62828;
}

.current-month {
  margin: 0;
  min-width: 200px;
  text-align: center;
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 5px;
}

.task-date {
  font-size: 12px;
  color: #666;
  white-space: nowrap;
  margin-left: 10px;
}

.event-item {
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 10px;
  border: 1px solid #dddddda1;
}

