}

.project-selector {
  margin-bottom: 20px;
  padding: 0 20px;
}

.project-select-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.project-select-wrapper label {
  font-size: 14px;
  color: #2c3e50;
  font-weight: 500;
}

.select-container {
  position: relative;
}

.project-select {
  width: 100%;
