<template>
  <div class="mobile-finance">
    <!--   -->
    <div class="wallet-card">
      <h3></h3>
      <div class="balance-info">
        <span class="balance-label">  </span>
        <span class="balance-amount">{{ availableFormatted }} .</span>
        <button class="withdraw-button" @click="requestPayout">
           
        </button>
      </div>
      
      <div class="balance-details">
        <div class="detail-item">
          <span class="detail-label">
            <span class="dot remaining"></span>
            
          </span>
          <span class="detail-value">{{ remainingFormatted }} .</span>
        </div>
        <div class="detail-item">
          <span class="detail-label">
            <span class="dot accrued"></span>
            
          </span>
          <span class="detail-value">{{ accruedFormatted }} .</span>
        </div>
        <div class="detail-item">
          <span class="detail-label">
            <span class="dot penalties"></span>
            
          </span>
          <span class="detail-value">{{ penaltiesFormatted }} .</span>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="transactions-card">
      <div class="transactions-header">
        <h3></h3>
        <div class="tabs">
          <button 
            v-for="tab in tabs" 
            :key="tab.id"
            :class="['tab-button', { active: activeTab === tab.id }]"
            @click="activeTab = tab.id"
          >
            {{ tab.name }}
          </button>
        </div>
      </div>

      <div class="transactions-list" v-if="transactions.length > 0">
        <div 
          v-for="transaction in transactions" 
          :key="transaction.id" 
          class="transaction-item"
        >
          <div class="transaction-info">
            <span class="transaction-type">{{ transaction.type }}</span>
            <span class="transaction-date">{{ formatDate(transaction.date) }}</span>
          </div>
          <span :class="['transaction-amount', transaction.amount > 0 ? 'positive' : 'negative']">
            {{ formatAmount(transaction.amount) }} .
          </span>
        </div>
      </div>
      
      <div v-else class="empty-transactions">
        <img src="@/icons/wallet.svg" alt="Empty transactions" />
        <p> </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MobileFinance',
  data() {
    return {
      total: 10000000,
      available: 2000000,
      accrued: 1000000,
      penalties: 100000,
      activeTab: 'all',
      tabs: [
        { id: 'all', name: '' },
        { id: 'accrued', name: '' },
        { id: 'withdrawn', name: '' },
        { id: 'penalties', name: '' }
      ],
      transactions: [] //     
    }
  },
  computed: {
    remaining() {
      return this.total - this.available - this.accrued - this.penalties
    },
    availableFormatted() {
      return this.formatNumber(this.available)
    },
    accruedFormatted() {
      return this.formatNumber(this.accrued)
    },
    penaltiesFormatted() {
      return this.formatNumber(this.penalties)
    },
    remainingFormatted() {
      return this.formatNumber(this.remaining)
    }
  },
  methods: {
    formatNumber(value) {
      return value.toLocaleString('ru-RU')
    },
    formatAmount(amount) {
      return Math.abs(amount).toLocaleString('ru-RU')
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long'
      })
    },
    requestPayout() {
      //   
      console.log(' ')
    }
  }
}
</script>

<style scoped>
.mobile-finance {
  padding: 16px;
  background-color: #e2f3ff;
  min-height: 100vh;
}

.wallet-card {
  background: white;
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 16px;
}

.wallet-card h3 {
  margin: 0 0 16px 0;
  font-size: 18px;
  color: #333;
}

.balance-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
}

.balance-label {
  font-size: 14px;
  color: #666;
  margin-bottom: 8px;
}

.balance-amount {
  font-size: 28px;
  font-weight: 600;
  color: #333;
  margin-bottom: 16px;
}

.withdraw-button {
  background: #539bff;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  width: 100%;
}

.balance-details {
  border-top: 1px solid #eee;
  padding-top: 16px;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.detail-label {
  display: flex;
  align-items: center;
  color: #666;
  font-size: 14px;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 8px;
}

.dot.remaining { background-color: #808080; }
.dot.accrued { background-color: #ffa500; }
.dot.penalties { background-color: #ff0000; }

.detail-value {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}

.transactions-card {
  background: white;
  border-radius: 16px;
  padding: 20px;
}

.transactions-header {
  margin-bottom: 20px;
}

.transactions-header h3 {
  margin: 0 0 16px 0;
  font-size: 18px;
  color: #333;
}

.tabs {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  padding-bottom: 8px;
}

.tab-button {
  white-space: nowrap;
  padding: 8px 16px;
  border: none;
  border-radius: 20px;
  background: #f5f5f5;
  color: #666;
  font-size: 14px;
}

.tab-button.active {
  background: #539bff;
  color: white;
}

.transactions-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.transaction-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #eee;
}

.transaction-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.transaction-type {
  font-size: 14px;
  color: #333;
}

.transaction-date {
  font-size: 12px;
