}

.transaction-amount {
  font-size: 14px;
  font-weight: 500;
}

.transaction-amount.positive {
  color: #27ae60;
}

.transaction-amount.negative {
  color: #e74c3c;
}

.empty-transactions {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px 0;
  color: #666;
}

.empty-transactions img {
  width: 48px;
  height: 48px;
  margin-bottom: 16px;
  opacity: 0.5;
}

.empty-transactions p {
  font-size: 14px;
  margin: 0;
}
</style> 