<!-- src/components/DocumentItem.vue -->
<template>
  <div class="document-item">
    <div class="document-info">
      <div class="document-label">{{ label }}</div>
    </div>
    <div class="document-actions">
      <button 
        v-if="isDocumentAvailable" 
        class="btn-download"
        @click="downloadDocument"
      >
        
      </button>
      <span v-else class="no-file"> </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    label: String,
    fileName: String,
    baseDownloadUrl: String,
    downloadFile: Function,
    profileData: Object,
    profileType: String,
  },
  computed: {
    isDocumentAvailable() {
      return this.profileData?.[`${this.profileType}_profile`]?.[this.fileName];
    },
    documentUrl() {
      if (!this.isDocumentAvailable) return null;
      return `${this.baseDownloadUrl}/${this.profileData[`${this.profileType}_profile`][this.fileName]}`;
    },
  },
  methods: {
    downloadDocument() {
      if (this.isDocumentAvailable) {
        this.downloadFile(this.fileName);
      }
    },
  },
};
</script>

<style scoped>
.document-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background: #f8f9fa;
  border-radius: 8px;
  gap: 10px;
}

.document-info {
  flex: 1;
  min-width: 0;
}

.document-label {
  font-size: 13px;
  color: #2c3e50;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.document-actions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.btn-download {
  padding: 6px 10px;
  font-size: 13px;
  white-space: nowrap;
  background: #539bff;
  color: white;
  border: none;
  border-radius: 6px;
}

.no-file {
  font-size: 12px;
  color: #999;
}
</style>
