<template>
  <div class="main-page">
    <MobileChats v-if="isMobile" />
    <Chats v-else />
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from "vue";
import Chats from "../Chats/ChatsContent.vue";
import MobileChats from "../Chats/MobileChats.vue";

export default {
  components: {
    Chats,
    MobileChats,
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  background-color: #e2f3ff;
  padding: 20px 300px;
  height: calc(100vh - 90px);
  overflow: hidden;
}

@media (max-width: 768px) {
  .main-page {
    margin-left: 0;
    padding: 0;
    height: 100vh;
  }
}
</style>
