<template>
  <div class="container">
    <div class="chats-page">
      <div class="chat-container">
        <div class="chat-main">
          <!--   -->
          <div class="chat-header" v-if="chatStore.selectedChat">
            <div class="chat-info">
              <img
                :src="
                  chatStore.selectedChat?.companion?.photo || 
                  chatStore.selectedChat?.participants[0]?.user?.photo ||
                  require('@/assets/avatar.png')
                "
                alt="Chat Photo"
                class="chat-avatar"
              />
              <div class="chat-header-text">
                <h4 class="chat-name">{{ participantName }}</h4>
                <p class="chat-meta">
                  {{ chatStore.selectedChat.participants[0].user.email }}
                </p>
              </div>
            </div>

            <div class="header-actions">
              <!--   -->
              <div v-if="isSearchOpen" class="search-input-wrapper">
                <img
                  src="@/icons/search.svg"
                  alt="Search"
                  class="search-icon"
                />
                <input
                  type="text"
                  v-model="searchQuery"
                  placeholder=" ..."
                  class="search-input"
                  @input="handleSearch"
                  ref="searchInput"
                />
                
                <!--     -->
                <div class="search-results" v-if="isSearchOpen && searchQuery">
                  <div class="results-count">
                     : {{ filteredMessages.length }}
                  </div>
                  <div class="results-list" v-if="filteredMessages.length">
                    <div
                      v-for="msg in filteredMessages"
                      :key="msg.id"
                      class="search-result-item"
                      @click="scrollToMessage(msg.id)"
                    >
                      <p>{{ msg.message }}</p>
                      <small>{{
                        chatStore.formatTime(msg.timestamp || msg.created_at)
                      }}</small>
                    </div>
                  </div>
                  <div v-else class="no-results">  </div>
                </div>
              </div>

              <div class="chat-actions">
                <button @click="toggleSearch" :class="{ active: isSearchOpen }">
                  <img 
                    :src="isSearchOpen ? require('@/icons/close.svg') : require('@/icons/search.svg')" 
                    :alt="isSearchOpen ? 'Close' : 'Search'" 
                  />
                </button>
                <button><img src="@/icons/calling.svg" alt="Call" /></button>
                <button><img src="@/icons/filter.svg" alt="More" /></button>
              </div>
            </div>
          </div>

          <!--   -->
          <div
            class="messages"
            v-if="chatStore.selectedChat"
            ref="messagesContainer"
          >
            <div
              v-for="group in chatStore.groupMessagesByDate(
                chatStore.getMessagesForCurrentChat,
              )"
              :key="group.date"
            >
              <div class="date-header">
                {{ chatStore.formatDateHeader(group.date) }}
              </div>
              <div
                v-for="msg in group.messages"
                :key="msg.id"
                :id="'message-' + msg.id"
                :class="{
                  'message-wrapper': true,
                  'my-message': msg.is_own,
                  'their-message': !msg.is_own,
                }"
              >
                <div class="message-bubble">
                  <div 
                    v-if="chatStore.selectedChat?.chat_type === 'group' && !msg.is_own" 
                    class="sender-name"
                  >
                    <span class="debug-info" style="display: none;">
                      SenderID: {{msg.sender_id}}
                    </span>
                    {{ chatStore.getSenderName(msg.sender_id || msg.sender) }}
                  </div>
                  <div class="message-content">
                    <p>{{ msg.message }}</p>
                    <small>{{
                      chatStore.formatTime(msg.timestamp || msg.created_at)
                    }}</small>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!--    -->
          <div class="message-input-container" v-if="chatStore.selectedChat">
            <div class="message-input-wrapper">
              <div class="emoji-container" ref="emojiButton">
                <img
                  src="@/icons/smile.svg"
                  alt="Emoji"
                  class="input-icon"
                  @click="toggleEmojiPicker"
                />
              </div>
              <input
                type="text"
                placeholder=" "
                v-model="chatStore.message"
                @keyup.enter="chatStore.sendMessage"
                class="styled-input"
              />
              <img
                src="@/icons/vector.svg"
                alt="Attachment"
                class="input-icon"
              />
            </div>
            <button class="send-button" @click="chatStore.sendMessage">
              <img 
                :src="chatStore.message ? require('@/icons/send.svg') : require('@/icons/voice.svg')" 
                :alt="chatStore.message ? 'Send' : 'Voice'" 
              />
            </button>
          </div>
        </div>

        <!--    -->
        <aside class="chat-sidebar">
          <div class="sidebar-header">
            <div class="tabs">
              <button
                :class="{ active: chatStore.selectedTab === 'contacts' }"
                @click="chatStore.selectedTab = 'contacts'"
              >
                
              </button>
              <button
                :class="{ active: chatStore.selectedTab === 'groups' }"
                @click="chatStore.selectedTab = 'groups'"
              >
                
              </button>
            </div>
          </div>
          <div class="chat-list">
            <div v-if="chatStore.selectedTab === 'contacts'">
              <div
                class="chat-item"
                v-for="(chat, index) in chatStore.privateChats"
                :key="chat.id"
                @click="chatStore.connectToChat(chat)"
                :class="{
                  active:
                    chatStore.selectedChat &&
                    chatStore.selectedChat.id === chat.id,
                }"
              >
                <div class="chat-info">
                  <img
                    :src="
                      chat.companion?.photo || 
                      chat.participants[0]?.user?.photo ||
                      require('@/assets/avatar.png')
                    "
                    alt="Chat Photo"
                    class="chat-avatar"
                  />
                  <div class="chat-text">
                    <h4>{{ sidebarParticipantName[index] }}</h4>
                    <p v-if="chat.last_message">
                      {{ chat.last_message.content }}
                      <span class="message-time">{{
                        chatStore.formatTime(chat.last_message.created_at)
                      }}</span>
                    </p>
                    <p v-else> </p>
                  </div>
                </div>
                <span class="unread-count" v-if="chat.unread">
                  {{ chat.unread }}
                </span>
              </div>
            </div>
            
            <!--     -->
            <div v-if="chatStore.selectedTab === 'groups'">
              <div
                class="chat-item"
                v-for="chat in chatStore.groupChats"
                :key="chat.id"
                @click="chatStore.connectToChat(chat)"
                :class="{
                  active: chatStore.selectedChat && chatStore.selectedChat.id === chat.id,
                }"
              >
                <div class="chat-info">
                  <img
                    :src="chat.photo || require('@/assets/avatar.png')"
                    alt="Group Photo"
                    class="chat-avatar"
                  />
                  <div class="chat-text">
                    <h4>{{ chat.name }}</h4>
                    <p class="participants-count">
                      {{ chat.participants?.length || 0 }} 
                    </p>
                    <p v-if="chat.last_message">
                      {{ chat.last_message.content }}
                      <span class="message-time">{{
                        chatStore.formatTime(chat.last_message.created_at)
                      }}</span>
                    </p>
                    <p v-else> </p>
                  </div>
                </div>
                <span class="unread-count" v-if="chat.unread">
                  {{ chat.unread }}
                </span>
              </div>
            </div>
          </div>
        </aside>
      </div>
    </div>
  </div>
</template>

<script>
import { useChatStore } from "@/store/chatStore";
import { EmojiButton } from "@joeattardi/emoji-button";
import { onMounted, ref, onBeforeUnmount, computed, nextTick } from "vue";

export default {
  setup() {
    const chatStore = useChatStore();
    const messagesContainer = ref(null);
    const emojiPicker = ref(null);
    const isSearchOpen = ref(false);
    const searchQuery = ref("");
    const filteredMessages = ref([]);
    const searchInput = ref(null);

    const participantName = computed(() => {
      if (!chatStore.selectedChat) return "";
      
      if (chatStore.selectedChat.chat_type === "group") {
        return chatStore.selectedChat.name;
      }
      
      //     companion
      if (chatStore.selectedChat.companion) {
        return chatStore.selectedChat.companion.full_name;
      }
      
      // Fallback     companion 
      return chatStore.selectedChat.participants[0].user.profile_type === "physical"
        ? `${chatStore.selectedChat.participants[0].user.first_name} ${chatStore.selectedChat.participants[0].user.last_name}`
        : chatStore.selectedChat.participants[0].user.company_name;
    });

    const sidebarParticipantName = computed(() => {
      return chatStore.privateChats.map((chat) => {
        if (chat.companion) {
          return chat.companion.full_name;
        }
        
        return chat.participants[0].user.profile_type === "physical"
          ? `${chat.participants[0].user.first_name} ${chat.participants[0].user.last_name}`
          : chat.participants[0].user.company_name;
      });
    });

    const toggleEmojiPicker = () => {
      if (!chatStore.picker) {
        chatStore.initEmojiPicker();
      }
      chatStore.emojiButtonRef = document.querySelector('.emoji-container');
      chatStore.toggleEmojiPicker();
    };

    const toggleSearch = () => {
      isSearchOpen.value = !isSearchOpen.value;
      if (!isSearchOpen.value) {
        searchQuery.value = "";
        filteredMessages.value = [];
      } else {
        nextTick(() => {
          searchInput.value?.focus();
        });
      }
    };

    const closeSearch = () => {
      isSearchOpen.value = false;
      searchQuery.value = "";
      filteredMessages.value = [];
    };

    const handleSearch = () => {
      if (!searchQuery.value.trim()) {
        filteredMessages.value = [];
        return;
      }

      filteredMessages.value = chatStore.searchMessages(searchQuery.value);
    };

    const scrollToMessage = (messageId) => {
      const message = chatStore.getMessageById(messageId);
      
      if (!message) return;

      const messageElement = document.getElementById(`message-${messageId}`);
      
      if (messageElement) {
        //   
        const highlighted = document.querySelector(".message-bubble.highlighted");
        if (highlighted) {
          highlighted.classList.remove("highlighted");
        }

        //  
        closeSearch();

        //   
        messageElement.scrollIntoView({
          behavior: "smooth",
          block: "center"
        });

        //  
        const messageBubble = messageElement.querySelector('.message-bubble');
        
        if (messageBubble) {
          messageBubble.classList.add("highlighted");
          setTimeout(() => {
            messageBubble.classList.remove("highlighted");
          }, 2000);
        }
      }
    };

    onMounted(() => {
      const token = localStorage.getItem("accessToken");
      if (token) {
        chatStore.setAccessToken(token);

        const currentUserId = localStorage.getItem("userId");
        chatStore.setCurrentUserId(currentUserId);

        chatStore.fetchChats();
      } else {
        // Handle missing access token
      }

      if (messagesContainer.value) {
        messagesContainer.value.addEventListener(
          "scroll",
          chatStore.handleScroll,
        );
      }
    });

    onBeforeUnmount(() => {
      if (emojiPicker.value) {
        emojiPicker.value.destroy();
      }
    });

    return {
      chatStore,
      messagesContainer,
      toggleEmojiPicker,
      participantName,
      sidebarParticipantName,
      isSearchOpen,
      searchQuery,
      filteredMessages,
      toggleSearch,
      closeSearch,
      handleSearch,
      scrollToMessage,
      searchInput,
    };
  },
};
</script>

