<template>
  <!-- Tabs for different categories -->
  <div class="transaction-tabs">
    <button
      class="tab"
      :class="{ active: activeTab === 'all' }"
      @click="switchTab('all')"
    >
       
    </button>
    <button
      class="tab"
      :class="{ active: activeTab === 'accrued' }"
      @click="switchTab('accrued')"
    >
      
    </button>
    <button
      class="tab"
      :class="{ active: activeTab === 'withdrawn' }"
      @click="switchTab('withdrawn')"
    >
      
    </button>
    <button
      class="tab"
      :class="{ active: activeTab === 'penalties' }"
      @click="switchTab('penalties')"
    >
      
    </button>
    <button
      class="tab"
      :class="{ active: activeTab === 'bonuses' }"
      @click="switchTab('bonuses')"
    >
      
    </button>
  </div>

  <div class="transactions-section">
    <!--   -->
    <div v-if="transactions.length === 0" class="empty-state">
      <h3> </h3>
      <p>       </p>
    </div>

    <!-- Transactions Table -->
    <table v-else-if="activeTab === 'all'" class="transaction-table">
      <thead>
        <tr>
          <th>ID </th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="transaction in transactions" :key="transaction.id">
          <td>{{ transaction.id }}</td>
          <td>{{ transaction.type }}</td>
          <td>{{ transaction.card }}</td>
          <td>{{ transaction.date }}</td>
          <td
            :class="{
              'positive-amount': transaction.amount > 0,
              'negative-amount': transaction.amount < 0,
            }"
          >
            {{ formatAmount(transaction.amount) }}
          </td>
          <td>
            <button class="icon-button">
              <i class="fas fa-download"></i>
            </button>
            <button class="icon-button">
              <i class="fas fa-eye"></i>
            </button>
          </td>
          <td>{{ transaction.status }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeTab: "all",
      transactions: [],
    };
  },
  methods: {
    switchTab(tab) {
      this.activeTab = tab;
    },
    formatAmount(amount) {
      return amount > 0
        ? `+${amount.toLocaleString()} .`
        : `${amount.toLocaleString()} .`;
    },
  },
};
</script>

<style scoped>
.transactions-section {
  background-color: white;
  padding: 54px;
  border-radius: 20px;
}

h2 {
  margin-bottom: 20px;
}

.transaction-tabs {
  display: flex;
  gap: 50px;
  margin-bottom: 20px;
}

.tab {
  padding: 10px 15px;
  background-color: transparent;
  font-size: 14px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.tab.active {
  background-color: #539bff;
  color: white;
}

.transaction-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.transaction-table th,
.transaction-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.positive-amount {
  color: #27ae60;
}

.negative-amount {
  color: #e74c3c;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  text-align: center;
}

.empty-state h3 {
  font-size: 18px;
  color: #333;
  margin-bottom: 8px;
}

.empty-state p {
  color: #666;
  font-size: 14px;
}

.pagination,
.page-number,
.page-nav,
.page-nav img {
  display: none;
}
</style>
