<template>
  <div class="page-container">
    <MobileAuthentication v-if="isMobile" />
    <template v-else>
      <!--     () -->
      <div class="left-panel">
        <h1 class="panel-title">PROJECT MANAGER</h1>
      </div>

      <!--       -->
      <div class="right-content">
        <div class="form-container">
          <div class="form-content">
            <!--     -->
            <div class="section-card">
              <h2 v-if="!isLegalEntity"> </h2>
              <h2 v-else>  </h2>
              <div class="photo-upload">
                <label
                  class="circle"
                  :class="{
                    'has-photo': photoPreview,
                    'has-error': errors.photo,
                  }"
                >
                  <input
                    type="file"
                    class="file-upload-input"
                    @change="handleFileUpload"
                    accept=".jpg,.jpeg,.png"
                    style="display: none"
                  />
                  <img
                    v-if="photoPreview"
                    :src="photoPreview"
                    alt="Preview"
                    class="preview-image"
                  />
                  <span v-else-if="errors.photo" class="upload-icon error">{{
                    errors.photo
                  }}</span>
                  <span v-else class="upload-icon"> </span>
                </label>
                <span class="photo-info"
                  > : 5 ,  : jpg, jpeg,
                  png</span
                >
              </div>

              <div class="input-group">
                <div v-for="(field, index) in formFields" :key="index">
                  <div v-if="field.key === 'phone'">
                    <div v-if="errors.phone" class="field-error">
                      {{ errors.phone }}
                    </div>
                    <input
                      type="text"
                      :class="{ 'has-error': errors.phone }"
                      :placeholder="`  ${field.label}`"
                      v-model="formData.phone"
                    />
                  </div>

                  <div
                    v-else-if="
                      field.key === 'lastName' ||
                      field.key === 'firstName' ||
                      field.key === 'middleName'
                    "
                  >
                    <div v-if="errors[field.key]" class="field-error">
                      {{ errors[field.key] }}
                    </div>
                    <input
                      type="text"
                      :class="{ 'has-error': errors[field.key] }"
                      :placeholder="`  ${field.label}`"
                      v-model="formData[field.key]"
                    />
                  </div>

                  <div v-else-if="field.key === 'iin'">
                    <div v-if="errors.iin" class="field-error">
                      {{ errors.iin }}
                    </div>
                    <input
                      type="text"
                      :class="{ 'has-error': errors.iin }"
                      :placeholder="`  ${field.label}`"
                      v-model="formData.iin"
                    />
                  </div>

                  <div v-else-if="field.key === 'company_name'">
                    <div v-if="errors.company_name" class="field-error">
                      {{ errors.company_name }}
                    </div>
                    <input
                      type="text"
                      :class="{ 'has-error': errors.company_name }"
                      :placeholder="` ${field.label}`"
                      v-model="formData.company_name"
                    />
                  </div>

                  <div v-else-if="field.key === 'bin'">
                    <div v-if="errors.bin" class="field-error">
                      {{ errors.bin }}
                    </div>
                    <input
                      type="text"
                      :class="{ 'has-error': errors.bin }"
                      :placeholder="` ${field.label}`"
                      v-model="formData.bin"
                    />
                  </div>

                  <div v-else-if="field.key === 'legal_entity_type'">
                    <div v-if="errors.legal_entity_type" class="field-error">
                      {{ errors.legal_entity_type }}
                    </div>
                    <div
                      v-for="(select, index) in legalSelects"
                      :key="`legal-${index}`"
                      class="multiselect-container"
                      :style="{ marginBottom: '24px' }"
                    >
                      <div v-if="errors[select.key]" class="field-error">
                        {{ errors[select.key] }}
                      </div>
                      <div
                        class="selected"
                        :class="{ valid: selected[select.key] }"
                        @click="toggleDropdown(`legal-${index}`)"
                      >
                        {{
                          selected[select.key]
                            ? select.options.find(
                                (option) => option.id === selected[select.key],
                              )?.name
                            : select.placeholder
                        }}
                      </div>
                      <div
                        class="dropdown"
                        v-if="showDropdownIndex === `legal-${index}`"
                      >
                        <ul>
                          <li
                            v-for="option in select.options"
                            :key="option.id"
                            @click.stop="selectOption(select.key, option.id)"
                            :class="{
                              selected: selected[select.key] === option.id,
                            }"
                          >
                            {{ option.name }}
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>

                <div v-if="!isLegalEntity" class="document-upload">
                  <div class="file-upload-container">
                    <label
                      class="file-upload-label"
                      :class="{ 'has-error': errors.scan_of_ID }"
                    >
                      <span class="file-upload-text">
                        {{
                          errors.scan_of_ID ||
                          uploadedFiles.scan_of_ID ||
                          "    "
                        }}
                        <span class="file-info">
                           : 5 ,  : jpg,
                          jpeg, png, pdf
                        </span>
                      </span>
                      <input
                        type="file"
                        class="file-upload-input"
                        @change="handleDocumentUpload($event, 'scan_of_ID')"
                        accept=".jpg,.jpeg,.png,.pdf"
                        style="display: none"
                      />
                    </label>
                  </div>

                  <div class="file-upload-container">
                    <label
                      class="file-upload-label"
                      :class="{ 'has-error': errors.person_with_ID }"
                    >
                      <span class="file-upload-text">
                        {{
                          errors.person_with_ID ||
                          uploadedFiles.person_with_ID ||
                          "     "
                        }}
                        <span class="file-info">
                           : 5 ,  : jpg,
                          jpeg, png
                        </span>
                      </span>
                      <input
                        type="file"
                        class="file-upload-input"
                        @change="handleDocumentUpload($event, 'person_with_ID')"
                        accept=".jpg,.jpeg,.png"
                        style="display: none"
                      />
                    </label>
                  </div>
                </div>
              </div>
            </div>

            <!--    .  -->
            <div v-if="!isLegalEntity" class="section-card">
              <h2> </h2>
              <div class="input-group">
                <div
                  v-for="(select, index) in physicalSelects"
                  :key="index"
                  class="multiselect-container"
                >
                  <div class="input-wrapper">
                    <div
                      class="selected"
                      :class="{
                        valid: selected[select.key],
                        'has-error': errors[select.key],
                      }"
                      @click="toggleDropdown(index)"
                    >
                      {{
                        selected[select.key]
                          ? select.options.find(
                              (option) => option.id === selected[select.key],
                            )?.name
                          : select.placeholder
                      }}
                    </div>
                    <div class="dropdown-wrapper">
                      <div class="dropdown" v-if="showDropdownIndex === index">
                        <ul>
                          <li
                            v-for="option in select.options"
                            :key="option.id"
                            @click.stop="
                              () => selectOption(select.key, option.id)
                            "
                            :class="{
                              selected: selected[select.key] === option.id,
                            }"
                          >
                            {{ option.name }}
                          </li>
                        </ul>
                      </div>
                    </div>
                    <div v-if="errors[select.key]" class="field-error">
                      {{ errors[select.key] }}
                    </div>
                  </div>
                </div>

                <div class="multiselect-container" @click="toggleProgramDropdown">
                  <div
                    class="selected"
                    :class="{ valid: selected.programs.length > 0 }"
                  >
                    {{
                      selectedProgramsNames.length
                        ? selectedProgramsNames
                        : " ,   "
                    }}
                  </div>
                  <div class="dropdown" v-if="showProgramDropdown">
                    <ul>
                      <li
                        v-for="program in programs"
                        :key="program.id"
                        @click.stop="toggleProgram(program.id)"
                        :class="{
                          selected: selected.programs.includes(program.id),
                        }"
                      >
                        {{ program.name }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>

            <!--    .  -->
            <div v-if="isLegalEntity" class="section-card">
              <h2> </h2>
              <div class="document-upload">
                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.license }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.license ||
                        uploadedFiles.license ||
                        " "
                      }}
                      <span class="file-info">
                         : 5 ,  : pdf
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handleLicenseUpload"
                      accept=".pdf"
                      style="display: none"
                    />
                  </label>
                </div>

                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.accreditation }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.accreditation ||
                        uploadedFiles.accreditation ||
                        " "
                      }}
                      <span class="file-info">
                         : 5 ,  : pdf
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handleAccreditationUpload"
                      accept=".pdf"
                      style="display: none"
                    />
                  </label>
                </div>

                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.project }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.project ||
                        uploadedFiles.project ||
                        " "
                      }}
                      <span class="file-info">
                         : 5 ,  : pdf, doc,
                        docx
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handleProjectUpload"
                      accept=".pdf,.doc,.docx"
                      style="display: none"
                    />
                  </label>
                </div>

                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.portfolio }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.portfolio ||
                        uploadedFiles.portfolio ||
                        "  "
                      }}
                      <span class="file-info">
                         : 5 ,  : jpg, jpeg,
                        png, pdf
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handlePortfolioUpload"
                      accept=".jpg,.jpeg,.png,.pdf"
                      style="display: none"
                    />
                  </label>
                </div>

                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.completion_acts }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.completion_acts ||
                        uploadedFiles.completion_acts ||
                        "   "
                      }}
                      <span class="file-info">
                         : 5 ,  : pdf
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handleActsUpload"
                      accept=".pdf"
                      style="display: none"
                    />
                  </label>
                </div>

                <div class="file-upload-container">
                  <label
                    class="file-upload-label"
                    :class="{ 'has-error': errors.expertise_confirmation }"
                  >
                    <span class="file-upload-text">
                      {{
                        errors.expertise_confirmation ||
                        uploadedFiles.expertise_confirmation ||
                        "  "
                      }}
                      <span class="file-info">
                         : 5 ,  : pdf
                      </span>
                    </span>
                    <input
                      type="file"
                      class="file-upload-input"
                      @change="handleExpertiseConfirmationUpload"
                      accept=".pdf"
                      style="display: none"
                    />
                  </label>
                </div>
              </div>
            </div>

            <!--   -->
            <button class="btn" @click="updateProfile" :disabled="isLoading">
              {{ isLoading ? "..." : "" }}
            </button>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import axios from "axios";
import { ref, onMounted, onBeforeUnmount } from 'vue';
import MobileAuthentication from './MobileAuthentication.vue';

const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5MB

export default {
  name: "IndividualReg",
  components: {
    MobileAuthentication
  },
  data() {
    return {
      isLegalEntity: false,
      registrationType: "physical",
      showDropdown: false,
      showProgramDropdown: false,
      programs: [],
      accessToken: "",
      selected: {
        specialty: null,
        education: null,
        experience: null,
        programs: [],
        legalType: null,
        category: null,
      },
      physicalSelects: [
        {
          key: "specialty",
          placeholder: "  ",
          options: [],
        },
        {
          key: "education",
          placeholder: "  ",
          options: [],
        },
        {
          key: "experience",
          placeholder: "   ",
          options: [],
        },
      ],
      legalSelects: [
        {
          key: "legalType",
          placeholder: "   ",
          options: [],
        },
        {
          key: "category",
          placeholder: " ",
          options: [],
        },
      ],
      errors: {},
      formData: {
        photo: null,
        firstName: "",
        lastName: "",
        middleName: "",
        iin: "",
        phone: "",
        documents: {
          scan_of_ID: null,
          person_with_ID: null,
        },
        company_name: "",
        bin: "",
        license: null,
        accreditation: null,
        project: null,
        portfolio: null,
        completion_acts: null,
        expertise_confirmation: null,
      },
      photoPreview: null,
      showDropdownIndex: null,
      isLoading: false,
      errorMessage: "",
      uploadedFiles: {
        scan_of_ID: "",
        person_with_ID: "",
        license: "",
        accreditation: "",
        project: "",
        portfolio: "",
        completion_acts: "",
        expertise_confirmation: "",
      },
    };
  },
  computed: {
    selectedProgramsNames() {
      if (!this.programs) return "";
      return this.programs
        .filter((program) => this.selected.programs.includes(program.id))
        .map((program) => program.name)
        .join(", ");
    },
    formFields() {
      return this.isLegalEntity
        ? [
            { key: "company_name", label: " " },
            { key: "bin", label: "" },
            { key: "legal_entity_type", label: "  " },
          ]
        : [
            { key: "lastName", label: "" },
            { key: "firstName", label: "" },
            { key: "middleName", label: "" },
            { key: "iin", label: "" },
            { key: "phone", label: " " },
          ];
    },
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  },
  created() {
    this.isLegalEntity = localStorage.getItem("profileType") === "legal";
    this.fetchUserProfile().catch((error) => {
      console.error("Error during component initialization:", error);
      this.errorMessage = "   ";
    });
  },
  methods: {
    camelToSnake(str) {
      return str.replace(/[A-Z]/g, (letter) => `_${letter.toLowerCase()}`);
    },
    removeErrorByField(fieldKey) {
      if (fieldKey in this.errors) {
        delete this.errors[fieldKey];
        this.errors = { ...this.errors };
      }
    },

    checkFileFormatAndSize(file, allowedTypes) {
      if (!file) return { valid: false, error: "  " };

      const fileExtension = "." + file.name.split(".").pop().toLowerCase();

      if (!allowedTypes.includes(fileExtension)) {
        return {
          valid: false,
          error: `  .  : ${allowedTypes.join(
            ", ",
          )}`,
        };
      }

      if (file.size > MAX_FILE_SIZE) {
        return {
          valid: false,
          error: "     5 ",
        };
      }

      return { valid: true };
    },

    handleFileChange(event, field, allowedTypes) {
      const file = event.target.files[0];
      if (!file) return;

      const { valid, error } = this.checkFileFormatAndSize(file, allowedTypes);

      //   ,   
      if (!valid) {
        this.errors[field] = error;
        this.uploadedFiles[field] = "";
        event.target.value = ""; //  input
        return;
      }

      //   ,     
      this.removeErrorByField(field);

      if (field === "photo") {
        const reader = new FileReader();
        reader.onload = () => {
          this.photoPreview = reader.result;
          this.formData[field] = file;
          this.uploadedFiles[field] = file.name;
        };
        reader.onerror = () => {
          this.errors[field] = "   ";
          this.uploadedFiles[field] = "";
        };
        reader.readAsDataURL(file);
      } else {
        if (field in this.formData.documents) {
          this.formData.documents[field] = file;
        } else {
          this.formData[field] = file;
        }
        this.uploadedFiles[field] = file.name;
      }
    },

    handleFileUpload(event) {
      this.handleFileChange(event, "photo", [".jpg", ".jpeg", ".png"]);
    },
    handleDocumentUpload(event, field) {
      if (field === "scan_of_ID") {
        this.handleFileChange(event, field, [".jpg", ".jpeg", ".png", ".pdf"]);
      } else if (field === "person_with_ID") {
        this.handleFileChange(event, field, [".jpg", ".jpeg", ".png"]);
      }
    },
    handleLicenseUpload(event) {
      this.handleFileChange(event, "license", [".pdf"]);
    },
    handleAccreditationUpload(event) {
      this.handleFileChange(event, "accreditation", [".pdf"]);
    },
    handleProjectUpload(event) {
      this.handleFileChange(event, "project", [".pdf", ".doc", ".docx"]);
    },
    handlePortfolioUpload(event) {
      this.handleFileChange(event, "portfolio", [
        ".jpg",
        ".jpeg",
        ".png",
        ".pdf",
      ]);
    },
    handleActsUpload(event) {
      //  completion_acts
      this.handleFileChange(event, "completion_acts", [".pdf"]);
    },
    handleExpertiseConfirmationUpload(event) {
      this.handleFileChange(event, "expertise_confirmation", [".pdf"]);
    },

    toggleDropdown(index) {
      this.showDropdownIndex = this.showDropdownIndex === index ? null : index;
      if (this.showDropdownIndex !== null) {
        this.showProgramDropdown = false;
      }
    },
    toggleProgramDropdown() {
      this.showProgramDropdown = !this.showProgramDropdown;
      if (this.showProgramDropdown) {
        this.showDropdownIndex = null;
      }
    },
    selectOption(key, value) {
      this.selected[key] = value;
      this.showDropdownIndex = null;
      this.removeErrorByField(key);
    },
    toggleProgram(programId) {
      const index = this.selected.programs.indexOf(programId);
      if (index === -1) {
        this.selected.programs.push(programId);
      } else {
        this.selected.programs.splice(index, 1);
      }
    },

    async fetchOptions(endpoint, key) {
      try {
        const response = await axios.get(
          `api/accounts/${endpoint}/`,
          { headers: { Authorization: `Bearer ${this.accessToken}` } },
        );

        //        options
        const select = this.physicalSelects.find((s) => s.key === key);
        if (select) {
          select.options = response.data.map((item) => ({
            id: item.id,
            name: item.name,
          }));
        }
      } catch {
        this.errorMessage = `   .`;
      }
    },

    async loadDropdownData() {
      if (this.isLegalEntity) {
        await this.fetchLegalTypes();
        await this.fetchCategories();
      } else {
        await Promise.all([
          this.fetchOptions("specialties", "specialty"),
          this.fetchOptions("educations", "education"),
          this.fetchOptions("experiences", "experience"),
          this.fetchPrograms(),
        ]);
      }
    },

    async fetchLegalTypes() {
      try {
        const response = await axios.get(
          "api/accounts/legal-entity-types/",
          { headers: { Authorization: `Bearer ${this.accessToken}` } },
        );
        if (Array.isArray(response.data)) {
          this.legalSelects.find((ls) => ls.key === "legalType").options =
            response.data.map((item) => ({
              id: item.id,
              name: item.name,
            }));
        }
      } catch (error) {
        console.error("     :", error);
        this.errorMessage = "     ";
      }
    },

    async fetchPrograms() {
      try {
        const response = await axios.get(
          "api/accounts/programs/",
          { headers: { Authorization: `Bearer ${this.accessToken}` } },
        );
        this.programs = response.data.map((item) => ({
          id: item.id,
          name: item.name,
        }));
      } catch {
        this.errorMessage = "    .";
      }
    },

    async fetchUserProfile() {
      const response = await axios.get(
        "api/accounts/profile/",
        {
          headers: { Authorization: `Bearer ${this.accessToken}` },
        },
      );
      if (response.data.legal_type) {
        this.$router.push("/dashboard");
        return;
      }
      this.populateFormData(response.data);
    },

    populateFormData(profile) {
      const physicalFields = [
        "firstName",
        "lastName",
        "middleName",
        "iin",
        "phone",
      ];
      const legalFields = ["company_name", "bin", "legal_entity_type"];

      this.formData.phone = profile.phone || "";

      if (!this.isLegalEntity) {
        physicalFields.forEach((field) => {
          if (field !== "phone") {
            this.formData[field] = profile[this.camelToSnake(field)] || "";
          }
        });

        this.selected.specialty = profile.specialty
          ? Number(profile.specialty)
          : null;
        this.selected.education = profile.education
          ? Number(profile.education)
          : null;
        this.selected.experience = profile.experience
          ? Number(profile.experience)
          : null;
        this.selected.programs = profile.programs
          ? profile.programs.map(Number)
          : [];

        if (profile.photo_url) this.photoPreview = profile.photo_url;

        if (profile.documents) {
          this.formData.documents.scan_of_ID =
            profile.documents.scan_of_ID || null;
          this.formData.documents.person_with_ID =
            profile.documents.person_with_ID || null;
        }
      } else {
        legalFields.forEach((field) => {
          this.formData[field] = profile[this.camelToSnake(field)] || "";
        });
      }
    },

    async updateProfileData(profileData) {
      const formData = new FormData();
      if (profileData.documents && typeof profileData.documents === "object") {
        Object.entries(profileData.documents).forEach(([key, file]) => {
          if (file) formData.append(key, file);
        });
      }

      Object.entries(profileData).forEach(([key, value]) => {
        if (
          key !== "documents" &&
          value !== "" &&
          value !== null &&
          value !== undefined
        ) {
          if (Array.isArray(value)) {
            value.forEach((item) => formData.append(key, item));
          } else {
            formData.append(key, value);
          }
        }
      });

      const response = await axios.put(
        "api/accounts/profile/edit/",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${this.accessToken}`,
          },
        },
      );
      return response.data;
    },

    handleUpdateErrors(error) {
      this.errors = {};
      if (error.response && error.response.data) {
        for (const [key, msgs] of Object.entries(error.response.data)) {
          if (Array.isArray(msgs)) {
            this.errors[key] = msgs.some((msg) =>
              msg.toLowerCase().includes("required"),
            )
              ? "    ."
              : msgs.join(", ");
          } else {
            this.errors[key] = msgs;
          }
        }
      } else {
        this.errors.general = "    .";
      }
    },

    async updateProfile() {
      const accessToken = localStorage.getItem("accessToken");
      if (!accessToken) {
        this.errors.general = ":   .";
        this.$router.push("/landing");
        return;
      }

      this.errors = {};
      const requiredFields = this.isLegalEntity
        ? ["company_name", "bin"]
        : ["firstName", "lastName", "phone", "iin"];

      requiredFields.forEach((field) => {
        if (!this.formData[field]?.trim()) {
          this.errors[field] = "    .";
        }
      });

      if (!this.isLegalEntity && !this.selected.specialty) {
        this.errors.specialty = "  .";
      }

      if (this.isLegalEntity && !this.selected.legalType) {
        this.errors.legalType = "   .";
      }

      if (this.isLegalEntity && !this.selected.category) {
        this.errors.category = " .";
      }

      if (Object.keys(this.errors).length > 0) return;

      const profileData = {
        phone: this.formData.phone.trim(),
        [this.isLegalEntity ? "photo_company" : "photo"]: this.formData.photo,
        documents: this.formData.documents,
      };

      if (!this.isLegalEntity) {
        profileData.first_name = this.formData.firstName.trim();
        profileData.last_name = this.formData.lastName.trim();
        profileData.middle_name = this.formData.middleName.trim();
        profileData.iin = this.formData.iin.trim();
        profileData.specialty = this.selected.specialty;
        profileData.education = this.selected.education;
        profileData.experience = this.selected.experience;
        profileData.programs = this.selected.programs;
      } else {
        profileData.company_name = this.formData.company_name.trim();
        profileData.bin = this.formData.bin.trim();
        profileData.legal_entity_type = this.selected.legalType;
        profileData.category = this.selected.category;
      }

      profileData.license = this.formData.license;
      profileData.accreditation = this.formData.accreditation;
      profileData.project = this.formData.project;
      profileData.portfolio = this.formData.portfolio;
      profileData.completion_acts = this.formData.completion_acts;
      profileData.expertise_confirmation = this.formData.expertise_confirmation;

      try {
        this.isLoading = true;
        await this.updateProfileData(profileData);
        localStorage.setItem("profileComplete", "true");
        localStorage.setItem(
          "profileType",
          this.isLegalEntity ? "legal" : "physical",
        );
        await this.$router.push("/dashboard");
      } catch (error) {
        console.error("   :", error);
        if (this.errors.general) {
          this.errorMessage = this.errors.general;
        } else {
          this.handleUpdateErrors(error);
        }
      } finally {
        this.isLoading = false;
      }
    },

    async fetchCategories() {
      try {
        const response = await axios.get(
          "api/accounts/categories/",
          { headers: { Authorization: `Bearer ${this.accessToken}` } },
        );
        if (Array.isArray(response.data)) {
          this.legalSelects.find((ls) => ls.key === "category").options =
            response.data.map((item) => ({
              id: item.id,
              name: item.name,
            }));
        }
      } catch (error) {
        console.error("   :", error);
        this.errorMessage = "   ";
      }
    },
  },
};
</script>

