<!-- src/components/ApexDoughnutChart.vue -->
<template>
  <div class="chart-container">
    <apexchart
      type="radialBar"
      :options="chartOptions"
      :series="series"
    ></apexchart>
    <div class="chart-center">
      <span class="total-amount"><br />{{ totalAmount }} .</span>
    </div>
  </div>
</template>

<script>
import { defineComponent, computed } from "vue";
import VueApexCharts from "vue3-apexcharts";

export default defineComponent({
  name: "ApexDoughnutChart",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    totalAmount: {
      type: String,
      required: true,
    },
    available: {
      type: Number,
      required: true,
    },
    accrued: {
      type: Number,
      required: true,
    },
    penalties: {
      type: Number,
      required: true,
    },
  },
  setup(props) {
    const total = props.available + props.accrued + props.penalties;

    const series = computed(() => [
      (props.available / total) * 100,
      (props.accrued / total) * 100,
      (props.penalties / total) * 100,
    ]);

    const chartOptions = computed(() => ({
      chart: {
        height: 350,
        type: "radialBar",
      },
      plotOptions: {
        radialBar: {
          hollow: {
            size: "70%",
          },
          dataLabels: {
            show: false,
          },
          track: {
            background: "#f0f0f0",
          },
        },
      },
      labels: ["", "", ""],
      colors: ["#539bff", "#ffa500", "#ff0000"],
      legend: {
        show: false,
      },
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              height: 300,
            },
            legend: {
              position: "bottom",
            },
          },
        },
      ],
    }));

    return {
      series,
      chartOptions,
    };
  },
});
</script>

<style scoped>
.chart-container {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.chart-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  pointer-events: none;
}

.total-amount {
  font-size: 18px;
  color: #000;
}
</style>
