<template>
  <div class="mobile-calendar">
    <!--   -->
    <div class="project-selector">
      <div class="project-select-wrapper">
        <label for="mobile-project-select"> :</label>
        <div class="select-container">
          <select 
            id="mobile-project-select"
            v-model="selectedProjectId" 
            class="project-select"
            @change="handleProjectChange"
          >
            <option value="" disabled> </option>
            <option 
              v-for="project in inProgressProjects" 
              :key="project.uuid"
              :value="project.uuid"
            >
              {{ project.name }}
            </option>
          </select>
          <div class="select-arrow"></div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="tabs">
      <button 
        @click="activeTab = 'calendar'"
        :class="['tab-btn', { active: activeTab === 'calendar' }]"
      >
        
      </button>
      <button 
        @click="activeTab = 'tasks'"
        :class="['tab-btn', { active: activeTab === 'tasks' }]"
      >
        
      </button>
    </div>

    <!--  -->
    <div v-if="activeTab === 'calendar'" class="calendar-content">
      <div class="calendar-header">
        <button @click="changeMonth(-1)">
          <img src="@/icons/left.svg" alt=" " />
        </button>
        <h2 class="current-month">
          {{ currentMonthName }} {{ currentYear }}
        </h2>
        <button @click="changeMonth(1)">
          <img src="@/icons/right.svg" alt=" " />
        </button>
      </div>

      <div class="calendar-grid">
        <div class="weekdays">
          <div v-for="day in ['', '', '', '', '', '', '']" 
               :key="day" 
               class="weekday">
            {{ day }}
          </div>
        </div>
        
        <div class="days">
          <div v-for="(cell, index) in weeks.flat()" 
               :key="index"
               :class="[
                 'day-cell',
                 { 
                   'empty': cell.monthOffset !== 0,
                   'today': isToday(cell),
                   'selected': isSelectedDay(cell),
                   'past-date': isPastDate(cell),
                   'has-tasks': getTasksForDay(cell.day).length > 0,
                   'task-green': hasTaskWithStatus(cell.day, 'green'),
                   'task-yellow': hasTaskWithStatus(cell.day, 'yellow'),
                   'task-red': hasTaskWithStatus(cell.day, 'red')
                 }
               ]"
               @click="cell.monthOffset === 0 && selectDay(cell.day)">
            <span class="day-number">{{ cell.day }}</span>
          </div>
        </div>
      </div>

      <div class="task-status-legend">
        <div class="legend-item">
          <span class="status-dot green"></span>
          <span> </span>
        </div>
        <div class="legend-item">
          <span class="status-dot yellow"></span>
          <span></span>
        </div>
        <div class="legend-item">
          <span class="status-dot red"></span>
          <span></span>
        </div>
      </div>
    </div>

    <!--   -->
    <div v-else class="tasks-content">
      <div class="tasks-header">
        <h3> </h3>
      </div>
      <div class="tasks-list">
        <div v-if="tasks.length === 0" class="empty-state">
          <img src="@/icons/calendar.svg" alt=" " class="empty-icon" />
          <p>  </p>
        </div>
        <div v-else v-for="task in sortedTasks" 
             :key="task.id" 
             class="task-item"
             :class="task.status">
          <div class="task-header">
            <div class="task-title">{{ task.title }}</div>
            <div class="task-date">{{ formatDate(task.due_date) }}</div>
          </div>
          <div class="task-description">{{ task.description }}</div>
          <div class="task-assignee">
            : {{ task.assigned_to }}
          </div>
        </div>
      </div>
    </div>

    <!--      -->
    <div v-if="showEventModal" class="modal-overlay" @click.self="closeEventModal">
      <div class="modal-content">
        <h3>   {{ selectedDay }} {{ currentMonthName }}</h3>
        
        <div class="task-form">
          <div class="form-group">
            <label for="taskTitle"> :</label>
            <input 
              id="taskTitle"
              v-model="taskTitle"
              type="text"
              placeholder="  "
            />
          </div>

          <div class="form-group">
            <label for="taskDescription"> :</label>
            <textarea
              id="taskDescription"
              v-model="taskDescription"
              placeholder="  "
            ></textarea>
          </div>

          <div class="members-list" v-if="projectMembers.length">
            <h4> :</h4>
            <div class="members-scroll">
              <div 
                v-for="member in projectMembers" 
                :key="member.id" 
                class="member-item"
                :class="{ 'selected': selectedMember === member.user_id }"
                @click="selectedMember = member.user_id"
              >
                <div class="member-photo">
                  <img :src="member.member.photo" :alt="member.member.first_name" 
                    @error="handleImageError" />
                </div>
                <div class="member-info">
                  <div class="member-name">
                    {{ member.member.last_name }} {{ member.member.first_name }}
                  </div>
                  <div class="member-role">{{ member.role }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="modal-actions">
          <button 
            @click="addEvent"
            :disabled="!taskTitle || !taskDescription || !selectedMember"
            class="create-btn"
          >
             
          </button>
          <button @click="closeEventModal" class="cancel-btn"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import 'dayjs/locale/ru';
import axios from 'axios';
import { useProjectStore } from '@/store/projects';

dayjs.locale('ru');

export default {
  name: 'MobileCalendar',
  data() {
    return {
      currentDate: dayjs(),
      today: dayjs(),
      weeks: [],
      selectedDay: null,
      activeTab: 'calendar',
      showEventModal: false,
      selectedProjectId: null,
      tasks: [],
      projectMembers: [],
      selectedMember: null,
      taskTitle: '',
      taskDescription: ''
    }
  },
  setup() {
    const projectStore = useProjectStore();
    return { projectStore };
  },
  computed: {
    inProgressProjects() {
      return this.projectStore.projects?.filter(p => p.status === 'in_progress') || [];
    },
    currentProject() {
      return this.projectStore.projects?.find(
        project => project.uuid === this.selectedProjectId
      );
    },
    currentMonthName() {
      return this.currentDate.format('MMMM').charAt(0).toUpperCase() + 
             this.currentDate.format('MMMM').slice(1);
    },
    currentYear() {
      return this.currentDate.year();
    },
    sortedTasks() {
      return [...this.tasks].sort((a, b) => {
        return dayjs(a.due_date).valueOf() - dayjs(b.due_date).valueOf();
      });
    }
  },
  methods: {
    generateCalendar() {
      const startOfMonth = this.currentDate.startOf('month');
      const daysInMonth = this.currentDate.daysInMonth();
      let startDay = startOfMonth.day();
      if (startDay === 0) startDay = 7;

      const weeks = [];
      let currentWeek = [];

      //    
      const prevMonth = this.currentDate.subtract(1, 'month');
      const daysInPrevMonth = prevMonth.daysInMonth();
      for (let i = startDay - 1; i > 0; i--) {
        currentWeek.push({
          day: daysInPrevMonth - i + 1,
          monthOffset: -1
        });
      }

      //    
      for (let day = 1; day <= daysInMonth; day++) {
        currentWeek.push({
          day,
          monthOffset: 0
        });

        if (currentWeek.length === 7) {
          weeks.push(currentWeek);
          currentWeek = [];
        }
      }

      //    
      let nextMonthDay = 1;
      while (currentWeek.length < 7) {
        currentWeek.push({
          day: nextMonthDay++,
          monthOffset: 1
        });
      }
      weeks.push(currentWeek);

      this.weeks = weeks;
    },
    async handleProjectChange() {
      console.log('Project changed to:', this.selectedProjectId);
      console.log('Current project object:', this.currentProject);
      
      this.tasks = [];
      this.selectedDay = null;
      this.showEventModal = false;

      if (this.selectedProjectId) {
        await this.fetchTasks();
        this.generateCalendar();
      }
    },
    async fetchTasks() {
      try {
        if (!this.selectedProjectId) {
          this.tasks = [];
          return;
        }

        const accessToken = localStorage.getItem('accessToken');
        const response = await axios.get('https:///api/tasks/', {
          headers: {
            'Authorization': `Bearer ${accessToken}`
          },
          params: {
            project: this.selectedProjectId
          }
        });
        
        const filteredTasks = response.data.filter(task => task.project === this.selectedProjectId);
        
        this.tasks = filteredTasks.map(task => ({
          ...task,
          status: this.getTaskStatus(task),
          assigned_to: task.assigned_to_details 
            ? `${task.assigned_to_details.last_name} ${task.assigned_to_details.first_name}`
            : '  '
        }));

        this.$nextTick(() => {
          this.generateCalendar();
        });
      } catch (error) {
        console.error('   :', {
          message: error.message,
          response: error.response?.data,
          status: error.response?.status
        });
        this.tasks = [];
      }
    },
    getTaskStatus(task) {
      const dueDate = dayjs(task.due_date);
      const now = dayjs();
      
      if (dueDate.isBefore(now)) {
        return 'red'; // 
      }
      
      const daysUntilDue = dueDate.diff(now, 'day');
      if (daysUntilDue <= 3) {
        return 'yellow'; //     ( 3   )
      }
      
      return 'green'; //  
    },
    handleImageError(e) {
      e.target.src = '___.jpg';
    },
    async addEvent() {
      try {
        if (!this.selectedProjectId) {
          console.error('  ');
          return;
        }

        const accessToken = localStorage.getItem('accessToken');
        const taskData = {
          title: this.taskTitle,
          description: this.taskDescription,
          assigned_to: this.selectedMember,
          project: this.selectedProjectId,
          due_date_str: dayjs(this.currentDate)
            .set('date', this.selectedDay)
            .format('YYYY-MM-DD')
        };

        await axios.post('https:///api/tasks/', taskData, {
          headers: {
            'Authorization': `Bearer ${accessToken}`
          }
        });

        await this.fetchTasks();
        this.closeEventModal();
      } catch (error) {
        console.error('   :', error);
      }
    },
    closeEventModal() {
      this.showEventModal = false;
      this.selectedDay = null;
      this.taskTitle = '';
      this.taskDescription = '';
      this.selectedMember = null;
    },
    changeMonth(delta) {
      this.currentDate = this.currentDate.add(delta, 'month');
      this.generateCalendar();
    },
    selectDay(day) {
      this.selectedDay = day;
    },
    isToday(cell) {
      if (cell.monthOffset !== 0) return false;
      return this.currentDate.date(cell.day).isSame(this.today, 'day');
    },
    isSelectedDay(cell) {
      if (cell.monthOffset !== 0) return false;
      return cell.day === this.selectedDay;
    },
    isPastDate(cell) {
      const dateToCheck = this.currentDate
        .add(cell.monthOffset, 'month')
        .date(cell.day);
      return dateToCheck.isBefore(this.today, 'day');
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long'
      });
    },
    getTasksForDay(day) {
      if (!this.tasks?.length) return [];
      
      return this.tasks.filter(task => {
        const taskDate = dayjs(task.due_date);
        return taskDate.date() === parseInt(day) &&
               taskDate.month() === this.currentDate.month() &&
               taskDate.year() === this.currentDate.year();
      });
    },
    hasTaskWithStatus(day, status) {
      if (!this.tasks?.length) return false;
      
      return this.tasks.some(task => {
        const taskDate = dayjs(task.due_date);
        return taskDate.date() === parseInt(day) &&
               taskDate.month() === this.currentDate.month() &&
               taskDate.year() === this.currentDate.year() &&
               task.status === status;
      });
    }
  },
  watch: {
    selectedProjectId: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.handleProjectChange();
        }
      },
      immediate: false
    },
    tasks: {
      handler() {
        this.$nextTick(() => {
          this.generateCalendar();
        });
      },
      deep: true
    }
  },
  async mounted() {
    this.generateCalendar();
    await this.projectStore.fetchProjects();
    
    const activeProjects = this.inProgressProjects;
    if (activeProjects.length > 0) {
      this.selectedProjectId = activeProjects[0].uuid;
      await this.fetchTasks();
    }
  }
}
</script>

