<template>
  <div class="mobile-activity-chart">
    <div v-if="projectStore.isLoading" class="loading-state">
      <p> ...</p>
    </div>
    
    <div v-else-if="projectStore.hasError" class="error-state">
      <p>   : {{ projectStore.error }}</p>
    </div>
    
    <div v-else-if="!isProjectInProgress" class="info-state">
      <p>      </p>
    </div>
    
    <template v-else>
      <!--     -->
      <div class="chart-header">
        <h3>  </h3>
        <select 
          v-model="selectedProjectId" 
          class="project-selector"
          @change="handleProjectChange"
        >
          <option 
            v-for="project in inProgressProjects" 
            :key="project.id" 
            :value="project.id"
          >
            {{ project.name }}
          </option>
        </select>
      </div>

      <!--  -->
      <div class="chart-legend">
        <div class="legend-item">
          <span class="legend-color work-day"></span>
          <span> </span>
        </div>
        <div class="legend-item">
          <span class="legend-color completed"></span>
          <span></span>
        </div>
      </div>

      <!--  -->
      <div class="months-selector">
        <button 
          v-for="(month, index) in months" 
          :key="month.name"
          :class="{ active: selectedMonth === index }"
          @click="selectMonth(index)"
          class="month-button"
        >
          {{ month.name }}
        </button>
      </div>

      <!--  -->
      <div class="tasks-container">
        <template v-for="(group, groupIndex) in workSchedule" :key="groupIndex">
          <div class="task-group">
            <div class="group-header" @click="toggleGroup(groupIndex)">
              <span class="group-id">#{{ group.prefix }}</span>
              <span class="group-title">{{ group.title }}</span>
              <span class="group-toggle" :class="{ 'is-open': openGroups[groupIndex] }">
                
              </span>
            </div>
            
            <div class="group-tasks" v-show="openGroups[groupIndex]">
              <div v-for="task in group.tasks" 
                   :key="task.id" 
                   class="task-item">
                <div class="task-header">
                  <span class="task-id">{{ task.order }}</span>
                  <span class="task-name">{{ task.name }}</span>
                  <span class="task-duration">5 .</span>
                </div>
                <div class="task-progress">
                  <div class="progress-bar">
                    <div 
                      class="progress-fill"
                      :style="{ width: getTaskProgress(task) + '%' }"
                      :class="{ completed: isTaskCompleted(task) }"
                    ></div>
                  </div>
                  <span class="progress-text">{{ getTaskProgress(task) }}%</span>
                </div>
                <div class="task-dates">
                  <span class="start-date">{{ getTaskStartDate(task) }}</span>
                  <span class="end-date">{{ getTaskEndDate(task) }}</span>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
import { useProjectStore } from "@/store/projects";
import { ref } from 'vue';

export default {
  name: "MobileActivityChart",
  setup() {
    const projectStore = useProjectStore();
    const selectedMonth = ref(0);
    const openGroups = ref({});

    return {
      projectStore,
      selectedMonth,
      openGroups
    };
  },
  data() {
    return {
      months: [
        { name: "", days: 31 },
        { name: "", days: 28 },
        { name: "", days: 31 },
        { name: "", days: 30 },
        { name: "", days: 31 }
      ],
      selectedProjectId: null
    };
  },
  computed: {
    inProgressProjects() {
      return this.projectStore.getProjects.filter(
        project => project.status === 'in_progress'
      );
    },
    currentProject() {
      return this.projectStore.getProjects.find(
        project => project.id === this.selectedProjectId
      );
    },
    workSchedule() {
      const defaultTasks = this.currentProject?.template?.default_tasks;
      
      if (!defaultTasks) return [];

      //    sub_work_name
      const groupedTasks = defaultTasks.reduce((acc, task) => {
        if (!acc[task.sub_work_name]) {
          //     order  prefix
          const prefix = task.order.split('.')[0];
          acc[task.sub_work_name] = {
            title: task.sub_work_name,
            prefix: prefix,
            tasks: []
          };
        }
        
        acc[task.sub_work_name].tasks.push({
          id: task.id,
          name: task.work_name,
          order: task.order,
          duration: task.duration || 5,
          startDate: task.start_date,
          endDate: task.end_date,
          progress: task.progress || 0,
          status: task.status || 'pending'
        });
        
        return acc;
      }, {});

      return Object.values(groupedTasks);
    },
    isProjectInProgress() {
      return this.inProgressProjects.length > 0;
    }
  },
  methods: {
    async loadProjectData() {
      await this.projectStore.fetchProjects();
      if (this.inProgressProjects.length > 0) {
        this.selectedProjectId = this.inProgressProjects[0].id;
      }
    },
    selectMonth(index) {
      this.selectedMonth = index;
    },
    toggleGroup(groupIndex) {
      this.openGroups[groupIndex] = !this.openGroups[groupIndex];
      this.$forceUpdate();
    },
    handleProjectChange() {
      //     
      this.loadProjectData();
    },
    getTaskProgress(task) {
      return task.progress || 0;
    },
    isTaskCompleted(task) {
      return task.status === 'completed' || task.progress === 100;
    },
    getTaskStartDate(task) {
      if (!task.startDate) return '-';
      return new Date(task.startDate).toLocaleDateString('ru-RU', {
        day: '2-digit',
        month: '2-digit',
        year: '2-digit'
      });
    },
    getTaskEndDate(task) {
      if (!task.endDate) return '-';
      return new Date(task.endDate).toLocaleDateString('ru-RU', {
        day: '2-digit',
        month: '2-digit',
        year: '2-digit'
      });
    }
  },
  created() {
    this.loadProjectData();
  }
};
</script>

<style scoped>
.mobile-activity-chart {
  background: white;
  border-radius: 12px;
  padding: 16px;
  margin-top: 16px;
}

.chart-header {
  margin-bottom: 16px;
}

.chart-header h3 {
  margin: 0 0 12px 0;
  font-size: 18px;
  color: #333;
}

.project-selector {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  color: #333;
  background-color: #fff;
}

.chart-legend {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
  padding: 8px 0;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  color: #666;
}

.legend-color {
  width: 12px;
  height: 12px;
  border-radius: 2px;
}

.legend-color.work-day {
  background-color: #BDBDBD;
}

.legend-color.completed {
  background-color: #539bff;
}

.months-selector {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  padding: 8px 0;
  margin-bottom: 16px;
  -webkit-overflow-scrolling: touch;
}

.month-button {
  padding: 8px 16px;
  border: 1px solid #ddd;
  border-radius: 20px;
  background: white;
  color: #666;
  font-size: 14px;
  white-space: nowrap;
}

.month-button.active {
  background: #539bff;
  color: white;
  border-color: #539bff;
}

.task-group {
  margin-bottom: 16px;
  border: 1px solid #eee;
  border-radius: 8px;
  overflow: hidden;
}

.group-header {
  padding: 12px;
  background: #f8f9fa;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.group-id {
  color: #666;
  margin-right: 8px;
  font-size: 14px;
}

.group-title {
  flex: 1;
  font-size: 14px;
  font-weight: 500;
}

.group-toggle {
  font-size: 12px;
  transition: transform 0.3s;
}

.group-toggle.is-open {
  transform: rotate(180deg);
}

.task-item {
  padding: 12px;
  border-bottom: 1px solid #eee;
}

.task-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.task-id {
  color: #666;
  font-size: 12px;
}

.task-name {
  flex: 1;
  font-size: 14px;
  color: #333;
}

.task-duration {
  font-size: 12px;
  color: #666;
}

.task-progress {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.progress-bar {
  flex: 1;
  height: 6px;
  background: #eee;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #BDBDBD;
  transition: width 0.3s ease;
}

.progress-fill.completed {
  background: #539bff;
}

.progress-text {
  font-size: 12px;
  color: #666;
  min-width: 40px;
  text-align: right;
}

.task-dates {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #666;
}

.loading-state,
.error-state,
.info-state {
  padding: 32px 16px;
  text-align: center;
  color: #666;
}

.error-state {
  color: #dc3545;
}

@media (max-width: 360px) {
  .task-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .task-duration {
    align-self: flex-end;
  }
}
</style> 