<script>
import { useProfileStore } from "@/store/profileStore";
import { onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import DocumentItem from "./DocumentItem.vue";

export default {
  components: {
    DocumentItem,
  },
  setup() {
    const profileStore = useProfileStore();
    const router = useRouter();

    onMounted(async () => {
      profileStore.accessToken = localStorage.getItem("accessToken");
      if (!profileStore.accessToken) {
        router.push("/landing");
      } else {
        profileStore.setupAxios();
        await profileStore.fetchUserProfile();
      }
    });

    const fullName = computed(() => {
      const { last_name, first_name, middle_name } =
        profileStore.profileData.physical_profile || {};
      return (
        [last_name, first_name, middle_name].filter(Boolean).join(" ") ||
        " "
      );
    });

    const isPhysicalProfile = computed(
      () => profileStore.profileType === "physical",
    );
    const isLegalProfile = computed(() => profileStore.profileType === "legal");

    const physicalDocuments = [
      { label: "  ", fileName: "scan_of_ID" },
      { label: "   ", fileName: "person_with_ID" },
    ];

    const legalDocuments = [
      { label: "", fileName: "license" },
      { label: "", fileName: "accreditation" },
      { label: "", fileName: "project" },
      { label: "", fileName: "portfolio" },
      { label: " ", fileName: "completion_acts" },
      { label: " ", fileName: "expertise_confirmation" },
    ];

    const getRandomHeight = () => {
      return Math.floor(Math.random() * 80) + 5; //    5%  85%
    };

    return {
      profileStore,
      fullName,
      isPhysicalProfile,
      isLegalProfile,
      physicalDocuments,
      legalDocuments,
      getRandomHeight,
    };
  },
};
</script>

