<template>
  <div class="projects-container">
    <!--   -->
    <div class="respondents-list pending">
      <h3> </h3>
      <div v-if="isLoading" class="loading">...</div>
      <div v-else-if="error" class="error">{{ error }}</div>
      <ul v-else-if="pendingResponses.length">
        <li v-for="response in pendingResponses" :key="response.id">
          <div class="respondent-item">
            <span class="respondent-name">
              {{
                [
                  response.specialist?.first_name,
                  response.specialist?.last_name,
                ]
                  .filter(Boolean)
                  .join(" ")
              }}
            </span>
            <span class="specialty">
              :
              <strong>{{ response.specialty_name || " " }}</strong>
            </span>
            <span class="application-date">
               :
              <strong>{{ formatDateTime(response.created_at) }}</strong>
            </span>
            <span class="project-name">
              : <strong>{{ response.project_name }}</strong>
            </span>
            <div class="button-group">
              <button class="action-button chat-button">  </button>
              <div class="approval-buttons">
                <button
                  class="action-button approve-button"
                  @click="acceptResponse(response.uuid)"
                  :disabled="isLoading"
                >
                  {{ isLoading ? "..." : "" }}
                </button>
                <button
                  class="action-button reject-button"
                  @click="rejectResponse(response.uuid)"
                  :disabled="isLoading"
                >
                  {{ isLoading ? "..." : "" }}
                </button>
              </div>
            </div>
          </div>
          <hr class="divider" />
        </li>
      </ul>
      <div v-else class="no-responses">  </div>
    </div>

    <!--     -->
    <div class="respondents-list accepted">
      <div class="header-with-toggle">
        <h3>
          {{
            showAccepted ? " " : " "
          }}
        </h3>
        <div class="toggle-buttons">
          <button
            class="toggle-button"
            :class="{ active: showAccepted }"
            @click="showAccepted = true"
          >
            
          </button>
          <button
            class="toggle-button"
            :class="{ active: !showAccepted }"
            @click="showAccepted = false"
          >
            
          </button>
        </div>
      </div>
      <ul v-if="showAccepted && acceptedResponses.length">
        <li v-for="response in acceptedResponses" :key="response.id">
          <div class="respondent-item accepted">
            <div class="specialist-info">
              <span class="respondent-name">
                {{
                  [
                    response.specialist?.first_name,
                    response.specialist?.last_name,
                  ]
                    .filter(Boolean)
                    .join(" ")
                }}
              </span>
              <span class="specialty">
                :
                <strong>{{ response.specialty_name || " " }}</strong>
              </span>
              <span class="project-name">
                : <strong>{{ response.project_name }}</strong>
              </span>
              <span class="acceptance-date">
                 :
                <strong>{{ formatDateTime(response.created_at) }}</strong>
              </span>
            </div>
            <div class="button-group">
              <button class="action-button chat-button">  </button>
            </div>
          </div>
          <hr class="divider" />
        </li>
      </ul>
      <div
        v-if="showAccepted && !acceptedResponses.length"
        class="no-responses"
      >
          
      </div>
      <div v-if="!showAccepted && rejectedResponses.length">
        <ul>
          <li v-for="response in rejectedResponses" :key="response.id">
            <div class="respondent-item rejected">
              <div class="specialist-info">
                <span class="respondent-name">
                  {{
                    [
                      response.specialist?.first_name,
                      response.specialist?.last_name,
                    ]
                      .filter(Boolean)
                      .join(" ")
                  }}
                </span>
                <span class="specialty">
                  :
                  <strong>{{ response.specialty_name || " " }}</strong>
                </span>
                <span class="project-name">
                  : <strong>{{ response.project_name }}</strong>
                </span>
                <span class="rejection-date">
                   :
                  <strong>{{ formatDateTime(response.created_at) }}</strong>
                </span>
              </div>
            </div>
            <hr class="divider" />
          </li>
        </ul>
      </div>
      <div
        v-if="!showAccepted && !rejectedResponses.length"
        class="no-responses"
      >
          
      </div>
    </div>

    <div class="auction-projects">
      <h3>  </h3>

      <!--  -->
      <div class="project-category" v-if="projectsByStatus.draft.length">
        <h4></h4>
        <ul>
          <li v-for="project in projectsByStatus.draft" :key="project.uuid">
            <div class="auction-item">
              <span class="auction-title">: {{ project.name }}</span>
              <span class="auction-subtitle">{{ project.subtitle }}</span>
              <span class="auction-description">{{ project.description }}</span>
              <span class="auction-date"
                > : {{ formatDateTime(project.created_at) }}</span
              >
              <span class="auction-complexity"
                >: {{ project.complexity?.name }}</span
              >
              <span class="auction-duration"
                >: {{ project.estimated_duration }} </span
              >
              <span class="project-status"> : </span>
              <button
                class="action-button work-button"
                @click="moveToAuction(project.uuid)"
              >
                  
              </button>
            </div>
            <hr class="divider" />
          </li>
        </ul>
      </div>

      <!--    -->
      <div class="project-category" v-if="projectsByStatus.auction.length">
        <h4> </h4>
        <ul>
          <li v-for="project in projectsByStatus.auction" :key="project.uuid">
            <div class="auction-item">
              <span class="auction-title">: {{ project.name }}</span>
              <span class="auction-subtitle">{{ project.subtitle }}</span>
              <span class="auction-description">{{ project.description }}</span>
              <span class="auction-date"
                > : {{ formatDateTime(project.created_at) }}</span
              >
              <span class="auction-complexity"
                >: {{ project.complexity?.name }}</span
              >
              <span class="auction-duration"
                >: {{ project.estimated_duration }} </span
              >
              <span class="project-status"> :  </span>
              <button
                class="action-button work-button"
                @click="moveToWork(project.uuid)"
              >
                  
              </button>
            </div>
            <hr class="divider" />
          </li>
        </ul>
      </div>

      <!--    -->
      <div class="project-category" v-if="projectsByStatus.in_progress.length">
        <h4> </h4>
        <ul>
          <li
            v-for="project in projectsByStatus.in_progress"
            :key="project.uuid"
          >
            <div class="auction-item">
              <span class="auction-title">: {{ project.name }}</span>
              <span class="auction-subtitle">{{ project.subtitle }}</span>
              <span class="auction-description">{{ project.description }}</span>
              <span class="auction-date"
                > : {{ formatDateTime(project.created_at) }}</span
              >
              <span class="auction-complexity"
                >: {{ project.complexity?.name }}</span
              >
              <span class="auction-duration"
                >: {{ project.estimated_duration }} </span
              >
              <span class="project-status"> :  </span>
            </div>
            <hr class="divider" />
          </li>
        </ul>
      </div>
    </div>

    <div class="create-project-container">
      <button
        v-if="!showCreateProject"
        @click="openCreateProject"
        class="create-project-button"
      >
          
      </button>
    </div>

    <div class="notification" v-if="showNotification">
      {{ notificationMessage }}
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import { useProjectStore } from "@/store/projects";
import { useResponsesStore } from "@/store/responses";

const router = useRouter();
const projectStore = useProjectStore();
const responsesStore = useResponsesStore();

const showCreateProject = ref(false);
const showAccepted = ref(true);

//    store
const pendingResponses = computed(() => responsesStore.pendingResponses);
const acceptedResponses = computed(() => responsesStore.acceptedResponses);
const rejectedResponses = computed(() => responsesStore.rejectedResponses);

//    store
const isLoading = computed(() => responsesStore.isLoading);
const error = computed(() => responsesStore.error);
const showNotification = computed(() => responsesStore.showNotification);
const notificationMessage = computed(() => responsesStore.notificationMessage);

const projectsByStatus = computed(() => {
  return {
    draft: projectStore.projects.filter((p) => p.status === "draft"),
    auction: projectStore.projects.filter((p) => p.status === "auction"),
    in_progress: projectStore.projects.filter((p) => p.status === "in_progress"),
  };
});

const openCreateProject = () => {
  router.push("/create-project");
  showCreateProject.value = true;
};

const acceptResponse = (uuid) => responsesStore.acceptResponse(uuid);
const rejectResponse = (uuid) => responsesStore.rejectResponse(uuid);

const formatDateTime = (dateString) => {
  return new Date(dateString).toLocaleString("ru-RU", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
  });
};

const moveToAuction = async (projectUuid) => {
  try {
    await responsesStore.moveToAuction(projectUuid);
    await projectStore.fetchProjects();
  } catch (error) {
    console.error("   :", error);
    error.value = "    ";
  }
};

const moveToWork = async (projectUuid) => {
  try {
    await responsesStore.moveToWork(projectUuid);
    await projectStore.fetchProjects();
  } catch (error) {
    console.error("     :", error);
    error.value = "      ";
  }
};

onMounted(async () => {
  await projectStore.fetchProjects();
  await responsesStore.fetchResponses();
});
</script>

