// /store/chatStore.js
import { defineStore } from "pinia";
import { computed, nextTick } from "vue";
import { EmojiButton } from "@joeattardi/emoji-button";

const ChatApi = {
  async fetchChats(token) {
    const response = await fetch("api/chat/chats/", {
      method: "GET",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
    });
    if (!response.ok) {
      throw new Error(`   : ${response.status}`);
    }
    return response.json();
  },

  async fetchChatHistory(token, chatId, page = 1) {
    if (!chatId) return null;
    const response = await fetch(
      `api/chat/chats/${chatId}/messages/?page=${page}`,
      {
        method: "GET",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
      }
    );
    if (!response.ok) {
      throw new Error(`    : ${response.status}`);
    }
    return response.json();
  },

  async fetchChatParticipants(token, chatId) {
    const response = await fetch(
      `api/chat/chats/${chatId}/participants/`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      }
    );
    return response.ok ? response.json() : null;
  },
};

export const useChatStore = defineStore("chat", {
  state: () => ({
    selectedTab: "contacts",
    searchQuery: "",
    chatsList: [],
    filteredChats: [],
    accessToken: null,
    selectedChat: null,
    message: "",
    messages: {},
    currentUserId: null,
    websocket: null,
    currentChatId: null,
    currentPage: 1,
    hasMoreMessages: true,
    picker: null,
    emojiButtonRef: null,
    chatParticipants: {},
  }),

  getters: {
    getMessagesForCurrentChat(state) {
      if (!state.selectedChat || !state.messages[state.selectedChat.id]) return [];
      return state.messages[state.selectedChat.id];
    },

    privateChats() {
      return this.filteredChatsComputed.filter((chat) => chat.chat_type === "private");
    },

    groupChats() {
      return this.filteredChatsComputed.filter((chat) => chat.chat_type === "group");
    },

    filteredChatsComputed(state) {
      const query = state.searchQuery.toLowerCase().trim();
      if (!query) return state.chatsList;
      return state.chatsList.filter((chat) => {
        const name = chat?.participants?.[0]?.user?.company_name?.toLowerCase() || "";
        const position = chat?.position?.toLowerCase() || "";
        return name.includes(query) || position.includes(query);
      });
    },

    getCompanyName: () => (chat) => {
      if (chat.chat_type === "group") {
        return chat.name || " ";
      }
      return chat?.participants?.[0]?.user?.company_name || " ";
    },
  },

  actions: {
    async fetchChats() {
      try {
        const data = await ChatApi.fetchChats(this.accessToken);
        this.chatsList = data;
        this.filteredChats = data;
      } catch (error) {
        console.error(" fetchChats:", error);
      }
    },

    async connectToChat(chat) {
      if (this.selectedChat && this.selectedChat.id === chat.id) {
        return;
      }

      this.messages[chat.id] = [];
      
      this.selectedChat = chat;
      this.currentChatId = chat.id;
      this.currentPage = 1;
      this.hasMoreMessages = true;
      await this.loadChatHistory(chat.id);

      nextTick(() => {
        const messagesContainer = document.querySelector('.messages');
        this.scrollToBottom(messagesContainer);
      });

      const wsUrl = `ws/chat/${chat.id}/?token=${this.accessToken}`;
      const reconnectWebSocket = () => {
        setTimeout(() => {
          if (this.currentChatId === chat.id) {
            connectWebSocket();
          }
        }, 5000);
      };

      const connectWebSocket = () => {
        try {
          this.websocket = new WebSocket(wsUrl);

          this.websocket.onopen = () => {
            // WebSocket connected
          };

          this.websocket.onmessage = (event) => {
            try {
              const data = JSON.parse(event.data);
              if (this.currentChatId === chat.id) {
                if (!this.messages[this.currentChatId]) {
                  this.messages[this.currentChatId] = [];
                }
                const isDuplicate = this.messages[this.currentChatId].some(
                  (msg) =>
                    msg.created_at === data.created_at &&
                    msg.message === data.message
                );
                if (!isDuplicate) {
                  const messageData = {
                    id: data.id || `msg-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
                    message: data.message,
                    sender_id: data.sender_id,
                    created_at: data.created_at,
                    is_own: data.is_own,
                    status: data.status,
                  };
                  this.messages[this.currentChatId].push(messageData);
                }
              }
            } catch (error) {
              // Handle onmessage error
            }
          };

          this.websocket.onerror = (error) => {
            reconnectWebSocket();
          };

          this.websocket.onclose = (event) => {
            if (this.currentChatId === chat.id && !event.wasClean) {
              reconnectWebSocket();
            }
          };
        } catch (error) {
          reconnectWebSocket();
        }
      };

      connectWebSocket();

      if (chat.chat_type === "group") {
        await this.loadChatParticipants(chat.id);
      }
    },

    sendMessage() {
      if (!this.message.trim()) return;
      if (!this.websocket) {
        return;
      }
      this.websocket.send(
        JSON.stringify({
          type: "chat_message",
          message: this.message,
          chat_id: this.selectedChat.id,
          chat_type: this.selectedChat.chat_type,
        })
      );
      this.message = "";
    },

    async loadChatHistory(chatId, page = 1) {
      try {
        const data = await ChatApi.fetchChatHistory(this.accessToken, chatId, page);
        if (!data || !data.results) return;

        if (!this.messages[chatId]) {
          this.messages[chatId] = [];
        }

        const formattedMessages = data.results.map((msg, index) => ({
          id: msg.id || `msg-${index}-${Date.now()}`,
          message: msg.message,
          sender_id: msg.sender_id,
          created_at: msg.created_at,
          is_own: msg.is_own ?? String(msg.sender_id) === this.currentUserId,
          status: msg.status,
        }));

        const newMessages = formattedMessages.reverse();

        if (page === 1) {
          this.messages[chatId] = newMessages;
        } else {
          this.messages[chatId] = [...newMessages, ...this.messages[chatId]];
        }

        this.currentPage = page;
        this.hasMoreMessages = data.results.length >= 50;
      } catch (error) {
        console.error('    :', error);
      }
    },

    handleScroll() {
      if (this.hasMoreMessages && this.selectedChat) {
        this.currentPage += 1;
        this.loadChatHistory(this.currentChatId, this.currentPage);
      }
    },

    formatTime(timestamp) {
      if (!timestamp) return "";
      const date = new Date(timestamp);
      return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
    },

    formatDateHeader(timestamp) {
      const date = new Date(timestamp);
      const today = new Date();
      const yesterday = new Date(today);
      yesterday.setDate(yesterday.getDate() - 1);
      if (date.toDateString() === today.toDateString()) return "";
      if (date.toDateString() === yesterday.toDateString()) return "";
      return date.toLocaleDateString("ru-RU", {
        day: "numeric",
        month: "long",
        year: "numeric",
      });
    },

    groupMessagesByDate(messages) {
      const groups = {};
      messages.forEach((msg) => {
        const date = new Date(msg.timestamp || msg.created_at).toDateString();
        if (!groups[date]) {
          groups[date] = [];
        }
        groups[date].push(msg);
      });
      return Object.entries(groups).map(([date, msgs]) => ({
        date: new Date(date),
        messages: msgs,
      }));
    },

    initEmojiPicker() {
      this.picker = new EmojiButton({
        position: "top-start",
        theme: "light",
        autoHide: false,
        emojisPerRow: 8,
        rows: 6,
        showPreview: false,
        showSearch: true,
        showCategoryButtons: true,
        i18n: {
          search: "",
          categories: {
            recents: "",
            smileys: "",
            people: "",
            animals: "",
            food: "",
            activities: "",
            travel: "",
            objects: "",
            symbols: "",
            flags: "",
          },
          notFound: "  ",
        },
      });
      this.picker.on("emoji", (selection) => {
        this.message += selection.emoji;
      });
    },

    toggleEmojiPicker() {
      if (this.picker && this.emojiButtonRef) {
        this.picker.togglePicker(this.emojiButtonRef);
      }
    },

    setAccessToken(token) {
      this.accessToken = token;
    },

    setCurrentUserId(userId) {
      this.currentUserId = String(userId);
    },

    scrollToBottom(element) {
      if (!element) return;
      element.scrollTop = element.scrollHeight;
    },

    searchMessages(query) {
      if (!query.trim() || !this.selectedChat || !this.messages[this.selectedChat.id]) {
        return [];
      }
      const msgs = this.messages[this.selectedChat.id];
      return msgs
        .filter((msg) => msg.message?.toLowerCase().includes(query.toLowerCase()))
        .map((msg, index) => ({
          id: msg.id || `msg-${index}-${Date.now()}`,
          message: msg.message,
          timestamp: msg.timestamp || msg.created_at,
          created_at: msg.created_at,
          sender_id: msg.sender_id,
          is_own: msg.is_own
        }));
    },

    getMessageById(messageId) {
      if (!this.selectedChat || !this.messages[this.selectedChat.id]) {
        return null;
      }
      return this.messages[this.selectedChat.id].find(
        (msg) => String(msg.id) === String(messageId)
      );
    },

    async loadChatParticipants(chatId) {
      try {
        const data = await ChatApi.fetchChatParticipants(this.accessToken, chatId);
        if (!data || data.error) {
          this.chatParticipants[chatId] = this.selectedChat.participants;
        } else {
          const participants = Array.isArray(data) ? data : data.participants || [];
          this.chatParticipants[chatId] = participants;
        }
      } catch (error) {
        this.chatParticipants[chatId] = this.selectedChat.participants;
      }
    },

    getSenderName(senderId) {
      if (!this.selectedChat) {
        return " ";
      }
      if (this.chatParticipants[this.selectedChat.id]) {
        const p = this.chatParticipants[this.selectedChat.id].find(
          (p) => String(p.user.user_id) === String(senderId)
        );
        if (p) {
          const u = p.user;
          return u.profile_type === "physical"
            ? `${u.first_name} ${u.last_name}`
            : u.company_name || "";
        }
      }
      const participant = this.selectedChat.participants.find(
        (p) => String(p.user.user_id) === String(senderId)
      );
      if (participant) {
        const u = participant.user;
        return u.profile_type === "physical"
          ? `${u.first_name} ${u.last_name}`
          : u.company_name || "";
      }
      return " ";
    },
  },
});
