<template>
  <div class="mobile-documents">
    <!--    -->
    <div class="search-section">
      <div class="search-box">
        <img src="@/icons/search.svg" alt="Search" class="search-icon" />
        <input
          type="text"
          v-model="searchQuery"
          placeholder=" ..."
          class="search-input"
        />
      </div>
      
      <div class="filters">
        <button
          class="filter-btn"
          :class="{ active: showFavorites }"
          @click="toggleFavoriteFilter"
        >
          <img src="@/icons/star.svg" alt="Star" />
        </button>
        <button
          class="filter-btn"
          :class="{ active: isPeriodsMenuOpen }"
          @click="togglePeriodsMenu"
        >
          <img src="@/icons/time.svg" alt="Time" />
        </button>
      </div>
    </div>

    <!--    -->
    <div v-if="isPeriodsMenuOpen" class="periods-menu">
      <div
        v-for="period in periods"
        :key="period"
        :class="['period-item', { active: selectedPeriod === period }]"
        @click="selectPeriod(period)"
      >
        {{ period }}
      </div>
    </div>

    <!--   -->
    <div class="documents-list">
      <!--  -->
      <div v-if="isLoading" class="loading-state">
        <div class="spinner"></div>
        <span> ...</span>
      </div>

      <!--  -->
      <div v-else-if="documents.length > 0" class="document-items">
        <div 
          v-for="(document, index) in documents" 
          :key="index" 
          class="document-card"
        >
          <div class="document-content">
            <div class="document-header">
              <h3 class="document-title">{{ document.title }}</h3>
              <button
                class="favorite-btn"
                :class="{ active: document.isFavorite }"
                @click="toggleFavorite(document)"
              >
                <img src="@/icons/star.svg" alt="Star" />
              </button>
            </div>
            <div class="document-footer">
              <span class="document-publisher">: {{ document.publisher }}</span>
              <div class="dates">
                <span class="document-date">
                  <img src="@/icons/calendar.svg" alt="Calendar" />
                  : {{ formatDate(document.publishDate) }}
                </span>
                <span class="document-date">
                  <img src="@/icons/calendar.svg" alt="Calendar" />
                  : {{ formatDate(document.updateDate) }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--   -->
      <div v-else class="empty-state">
        <p>  </p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';

export default {
  name: 'MobileDocuments',
  setup() {
    const searchQuery = ref('');
    const isPeriodsMenuOpen = ref(false);
    const selectedPeriod = ref(' ');
    const showFavorites = ref(false);
    const isLoading = ref(false);

    const periods = [' ', ' ', ' '];

    //    
    const documents = ref([
      {
        title: '          ,   ,     ',
        publisher: 'TengriNews',
        publishDate: '2023-10-24T12:12:00',
        updateDate: '2023-10-24T12:12:00',
        isFavorite: false
      },
      //     
    ]);

    const togglePeriodsMenu = (event) => {
      event.stopPropagation();
      isPeriodsMenuOpen.value = !isPeriodsMenuOpen.value;
    };

    const selectPeriod = (period) => {
      selectedPeriod.value = period;
      isPeriodsMenuOpen.value = false;
    };

    const toggleFavoriteFilter = () => {
      showFavorites.value = !showFavorites.value;
    };

    const toggleFavorite = (document) => {
      document.isFavorite = !document.isFavorite;
    };

    const formatDate = (date) => {
      return new Date(date).toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long',
        hour: '2-digit',
        minute: '2-digit'
      });
    };

    onMounted(() => {
      window.addEventListener('click', () => {
        isPeriodsMenuOpen.value = false;
      });
    });

    return {
      searchQuery,
      isPeriodsMenuOpen,
      selectedPeriod,
      showFavorites,
      isLoading,
      periods,
      documents,
      togglePeriodsMenu,
      selectPeriod,
      toggleFavoriteFilter,
      toggleFavorite,
      formatDate
    };
  }
};
</script>

<style scoped>
.mobile-documents {
  padding: 16px;
  background-color: #e2f3ff;
  min-height: 100vh;
}

.search-section {
  display: flex;
  gap: 12px;
  margin-bottom: 16px;
}

.search-box {
  flex: 1;
  display: flex;
  align-items: center;
  background: white;
  border-radius: 8px;
  padding: 8px 12px;
}

.search-icon {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  margin-right: 8px;
}

.search-input {
  border: none;
  outline: none;
  width: 100%;
  font-size: 14px;
}

.filters {
  display: flex;
  gap: 8px;
}

.filter-btn {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 8px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.filter-btn img {
  width: 20px;
  height: 20px;
  opacity: 0.5;
}

.filter-btn.active {
  background: #539bff;
}

.filter-btn.active img {
  opacity: 1;
  filter: brightness(0) invert(1);
}

.periods-menu {
  background: white;
  border-radius: 8px;
  margin-bottom: 16px;
  overflow: hidden;
}

.period-item {
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
  font-size: 14px;
  cursor: pointer;
}

.period-item:last-child {
  border-bottom: none;
}

.period-item.active {
  color: #539bff;
}

.document-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 16px;
}

.document-content {
  padding: 16px;
}

.document-header {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}

.document-title {
  flex: 1;
  margin: 0;
  font-size: 16px;
  line-height: 1.4;
}

.favorite-btn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
  background: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.favorite-btn img {
  width: 16px;
  height: 16px;
  opacity: 0.5;
}

.favorite-btn.active {
  background: #539bff;
}

.favorite-btn.active img {
  opacity: 1;
  filter: brightness(0) invert(1);
}

.document-footer {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.document-publisher {
  font-size: 14px;
  color: #666;
}

.dates {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.document-date {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 12px;
  color: #666;
}

.document-date img {
  width: 14px;
  height: 14px;
  opacity: 0.5;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 48px 0;
  color: #666;
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #eee;
  border-top: 3px solid #539bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

.empty-state {
  text-align: center;
  padding: 48px 0;
  color: #666;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}
</style> 