// src/store/profileStore.js
import { defineStore } from "pinia";
import axios from "axios";
import { useRouter } from "vue-router"; //  useRouter  

export const useProfileStore = defineStore("profile", {
  state: () => ({
    profileType: null,
    profileData: {
      physical_profile: {},
      legal_profile: {},
    },
    accessToken: "",
    programs: [],
    specialties: [],
    educations: [],
    experiences: [],
    legalEntityTypes: [],
    categories: [],
    showConfirmModal: false,
    baseDownloadUrl: "api/accounts/",
    apiClient: null,
  }),
  getters: {
    getSpecialtyName(state) {
      if (state.profileData.physical_profile?.is_gip === true) {
        return "";
      }

      const specialty = state.specialties.find(
        (item) => item.id === state.profileData.physical_profile?.specialty,
      );
      return specialty ? specialty.name : " ";
    },
    getEducationName(state) {
      const education = state.educations.find(
        (item) => item.id === state.profileData.physical_profile.education,
      );
      return education ? education.name : " ";
    },
    getExperienceName(state) {
      const experience = state.experiences.find(
        (item) => item.id === state.profileData.physical_profile.experience,
      );
      return experience ? experience.name : " ";
    },
    getCompanyName(state) {
      return state.profileData.legal_profile.company_name || " ";
    },
    getBin(state) {
      return state.profileData.legal_profile.bin || " ";
    },
    getLegalEntityTypeName(state) {
      const type = state.legalEntityTypes.find(
        (item) => item.id === state.profileData.legal_profile.legal_entity_type,
      );
      return type ? type.name : " ";
    },
    getCategoryName(state) {
      const category = state.categories.find(
        (item) => item.id === state.profileData.legal_profile.category,
      );
      return category ? category.name : " ";
    },
    selectedProgramsNames(state) {
      const names = state.programs
        .filter((program) =>
          state.profileData.physical_profile.programs.includes(program.id),
        )
        .map((program) => program.name)
        .join(", ");
      return names;
    },
  },
  actions: {
    /**
     *  axios   URL  .
     */
    setupAxios() {
      this.apiClient = axios.create({
        baseURL: this.baseDownloadUrl,
        headers: {
          "Content-Type": "application/json",
        },
      });

      //      
      this.apiClient.interceptors.request.use(
        (config) => {
          if (this.accessToken) {
            config.headers.Authorization = `Bearer ${this.accessToken}`;
          }
          return config;
        },
        (error) => {
          return Promise.reject(error);
        },
      );
    },

    /**
     *   .
     */
    async fetchUserProfile() {
      try {
        // Ensure apiClient is set up before making requests
        if (!this.apiClient) {
          this.setupAxios();
        }

        const response = await this.apiClient.get("profile/");
        this.profileData = response.data;
        this.profileType = response.data.profile_type;

        await this.loadDropdownData();
      } catch (error) {
        const router = useRouter();
        router.push("/landing");
      }
    },

    /**
     *     .
     */
    async loadDropdownData() {
      // Ensure apiClient is set up before making requests
      if (!this.apiClient) {
        this.setupAxios();
      }

      const endpoints = {
        educations: "educations/",
        specialties: "specialties/",
        experiences: "experiences/",
        programs: "programs/",
        legalEntityTypes: "legal-entity-types/",
        categories: "categories/",
      };

      try {
        const requests = Object.entries(endpoints).map(([key, value]) =>
          this.fetchData(value, key).catch((error) => {
            // Return an empty array to prevent Promise.all from rejecting
            return [];
          }),
        );

        await Promise.all(requests);
      } catch (error) {
        // Handle error loading dropdown data
      }
    },

    /**
     *     .
     * @param {String} endpoint -  API.
     * @param {String} target -   state   .
     */
    async fetchData(endpoint, target) {
      try {
        const response = await this.apiClient.get(endpoint);
        this[target] = response.data;
      } catch (error) {
        // Handle error loading data
      }
    },

    /**
     *   .
     */
    confirmSwitchProfile() {
      this.showConfirmModal = true;
    },

    /**
     *   .
     */
    cancelSwitch() {
      this.showConfirmModal = false;
    },

    /**
     *    .
     */
    async switchProfile() {
      this.showConfirmModal = false;
      try {
        await this.apiClient.patch("profile/switch-type/", {
          profile_type: "legal",
        });
        await this.fetchUserProfile();
        const router = useRouter();
        router.push("/cauth");
      } catch (error) {
        alert("    .  .");
      }
    },

    /**
     *    .
     * @param {String} type -   (, 'photo', 'license'  ..).
     */
    async downloadFile(type) {
      try {
        const endpoint = `download/${type}/`;

        const response = await this.apiClient.get(endpoint, {
          responseType: "blob", //  
        });

        //     
        const disposition = response.headers["content-disposition"];
        let filename = "downloaded_file";
        if (disposition && disposition.includes("attachment")) {
          const filenameMatch = disposition.match(
            /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/,
          );
          if (filenameMatch != null && filenameMatch[1]) {
            filename = filenameMatch[1].replace(/['"]/g, "");
          }
        }

        //      ,      
        if (!disposition) {
          filename = `${type}.${this.getFileExtension(type)}`;
        }

        //    
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", filename);
        document.body.appendChild(link);
        link.click();
        link.remove();
        window.URL.revokeObjectURL(url); //  
      } catch (error) {
        alert("   .  .");
      }
    },

    /**
     *      .
     * @param {String} type -  .
     * @returns {String}  .
     */
    getFileExtension(type) {
      const extensions = {
        photo: "jpg",
        scan_of_ID: "pdf",
        person_with_ID: "jpg",
        photo_company: "jpg",
        license: "pdf",
        accreditation: "pdf",
        project: "pdf",
        portfolio: "pdf",
        completion_acts: "pdf",
        expertise_confirmation: "pdf",
        //        
      };
      return extensions[type] || "dat";
    },
  },
});
