<template>
  <div>
    <MobileDocuments v-if="isMobile" />
    <div v-else class="main-page">
      <DocumentsContent />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import DocumentsContent from "../Documents/DocumentsContent.vue";
import MobileDocuments from "../Documents/MobileDocuments.vue";

export default {
  components: {
    DocumentsContent,
    MobileDocuments
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  padding: 20px;
  background-color: #e2f3ff;
  border-radius: 10px;
  padding: 20px 250px;
}

@media (max-width: 768px) {
  .main-page {
    margin-left: 0;
    padding: 0;
  }
}
</style>
