<template>
  <div>
    <div v-if="isMobile">
      <MobileCalendar />
      <MobileActivityChart />
    </div>
    <div v-else class="main-page">
      <Calendar />
      <ActivityChart />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from 'vue';
import Calendar from "../Calendar/CalendarContent.vue";
import ActivityChart from "../Calendar/ActivityChart.vue";
import MobileCalendar from "../Calendar/MobileCalendar.vue";
import MobileActivityChart from "../Calendar/MobileActivityChart.vue";

export default {
  components: {
    Calendar,
    ActivityChart,
    MobileCalendar,
    MobileActivityChart
  },
  setup() {
    const isMobile = ref(window.innerWidth <= 768);

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768;
    };

    onMounted(() => {
      window.addEventListener('resize', checkDevice);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', checkDevice);
    });

    return {
      isMobile
    };
  }
};
</script>

<style scoped>
.main-page {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  background-color: #e2f3ff;
  padding: 20px 300px;
}
</style>
