  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 2px dashed #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  overflow: hidden;
  margin-bottom: 10px;
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo-info {
  font-size: 12px;
  color: #666;
  text-align: center;
}

.form-fields {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-field input {
  width: 95%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
}

.field-error {
  color: #f44336;
  font-size: 12px;
  margin-top: 4px;
}

.document-upload {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}

.upload-label {
  display: block;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  cursor: pointer;
}

.select-fields {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.select-button {
  width: 95%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.select-text {
  color: #333;
  font-size: 14px;
}

.select-arrow {
  width: 16px;
  height: 16px;
  opacity: 0.5;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: flex-end;
  z-index: 1000;
}

.modal-content {
  width: 95%;
  background: white;
  border-radius: 16px 16px 0 0;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 16px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  font-size: 18px;
  color: #333;
}

.close-button {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
}

.close-button img {
  width: 20px;
  height: 20px;
}

.modal-body {
  overflow-y: auto;
  padding: 16px;
