// router/index.js
import { createRouter, createWebHistory } from "vue-router";
import Landing from "@/components/Landing.vue";
import Authentication from "@/components/Auth/Authentication.vue";
import Wrapper from "@/components/Layot/Wrapper.vue";
import MobileWrapper from "@/components/Layot/MobileWrapper.vue";
import Chats from "@/components/Pages/Chats.vue";
import Auction from "@/components/Pages/Auction.vue";
import News from "@/components/Pages/News.vue";
import Documents from "@/components/Pages/Documents.vue";
import Calendar from "@/components/Pages/Calendar.vue";
import Dashboard from "@/components/Pages/Dashboard.vue";
import Finance from "@/components/Pages/Finance.vue";
import Investments from "@/components/Pages/Investments.vue";
import Shop from "@/components/Pages/Shop.vue";
import Profile from "@/components/Profile/Profile.vue";
import Projects from "@/components/Pages/Project.vue";
import CreateProject from "@/views/CreateProject.vue";
import { defineComponent, h } from 'vue';

const DynamicWrapper = defineComponent({
  name: 'DynamicWrapper',
  data() {
    return {
      windowWidth: window.innerWidth
    }
  },
  computed: {
    currentWrapper() {
      return this.windowWidth <= 768 ? MobileWrapper : Wrapper;
    }
  },
  created() {
    window.addEventListener('resize', this.onResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.onResize);
  },
  methods: {
    onResize() {
      this.windowWidth = window.innerWidth;
    }
  },
  render() {
    return h(this.currentWrapper);
  }
});

const routes = [
  {
    path: "",
    component: DynamicWrapper,
    children: [
      { path: "", redirect: "landing" },
      {
        path: "dashboard",
        name: "Dashboard",
        component: Dashboard,
        meta: { requiresAuth: true },
      },
      {
        path: "chats",
        name: "Chats",
        component: Chats,
        meta: { requiresAuth: true },
      },
      {
        path: "projects",
        name: "Projects",
        component: Projects,
        meta: { requiresAuth: true },
      },
      {
        path: "auction",
        name: "Auction",
        component: Auction,
        meta: { requiresAuth: true },
      },
      {
        path: "news",
        name: "News",
        component: News,
        meta: { requiresAuth: true },
      },
      {
        path: "documents",
        name: "Documents",
        component: Documents,
        meta: { requiresAuth: true },
      },
      {
        path: "calendar",
        name: "Calendar",
        component: Calendar,
        meta: { requiresAuth: true },
      },
      {
        path: "finance",
        name: "Finance",
        component: Finance,
        meta: { requiresAuth: true },
      },
      {
        path: "investments",
        name: "Investments",
        component: Investments,
        meta: { requiresAuth: true },
      },
      {
        path: "shop",
        name: "Shop",
        component: Shop,
        meta: { requiresAuth: true },
      },
      {
        path: "/profile",
        name: "Profile",
        component: Profile,
        meta: { requiresAuth: true },
      },
    ],
  },
  {
    path: "/landing",
    name: "landing",
    component: () => import("@/views/HomeView.vue"),
    beforeEnter: () => {
      localStorage.removeItem("accessToken");
      localStorage.removeItem("refreshToken");
      return true;
    },
  },
  {
    path: "/authentication",
    name: "Authentication",
    component: Authentication,
  },
  {
    path: "/create-project",
    name: "CreateProject",
    component: CreateProject,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

import { useUserStore } from "@/store/user";

router.beforeEach(async (to, from, next) => {
  const userStore = useUserStore();
  const token = localStorage.getItem("accessToken");
  const profileComplete = localStorage.getItem("profileComplete") === "true";

  //    
  if (to.meta.requiresAuth) {
    if (!token) {
      next("/landing");
      return;
    }

    const isInitialized = await userStore.initialize();
    if (!isInitialized) {
      next("/landing");
      return;
    }
  }

  //   ,    /authentication
  if (profileComplete && to.path === "/authentication") {
    next("/dashboard");
    return;
  }

  //   ,    
  if (
    token &&
    !profileComplete &&
    !["/landing", "/authentication"].includes(to.path)
  ) {
    next("/authentication");
    return;
  }

  next();
});

export default router;
