<template>
  <div class="content-auction">
    <div v-if="allProjects && allProjects.length > 0" class="projects-list">
      <div
        v-for="project in allProjects"
        :key="project.uuid"
        class="project-container"
      >
        <div class="header">
          <div class="title-section">
            <h1 class="project-title">{{ project.name }}</h1>
            <span class="project-subtitle">{{ project.subtitle }}</span>
            <span class="category">{{ project.complexity.name }}</span>
          </div>
          <div class="meta-info">
            <div class="info-item">
              <i class="icon-clock"></i>
              <span>: {{ project.estimated_duration }} .</span>
            </div>
            <div class="info-item">
              <span class="client">: {{ project.client_name }}</span>
            </div>
          </div>
        </div>

        <div class="content-section">
          <div class="description">
            <h3> </h3>
            <p>{{ project.description }}</p>
          </div>

          <div class="proposal">
            <div class="budget-section">
              <h3></h3>
              <div class="specialty-budgets">
                <div v-for="budget in filteredBudgets(project.specialty_budgets)" 
                     :key="budget.specialty" 
                     class="specialty-budget">
                  <span class="specialty-name">
                    {{ getSpecialtyName(budget.specialty) }}:
                  </span>
                  <span class="budget">{{ formatBudget(budget.budget) }} .</span>
                </div>
              </div>
            </div>
            <div class="position-section">
              <h3></h3>
              <span class="position">{{ getCurrentSpecialty }}</span>
            </div>
            <button 
              class="btn-submit" 
              @click.stop="selectProject(project.uuid)"
            >
               
            </button>
          </div>
        </div>

        <div class="footer">
          <span>: {{ formatDate(project.created_at) }}</span>
        </div>
      </div>
    </div>
    <div v-else class="no-projects">
             
    </div>
  </div>
  <div class="buttons">
    <!-- <button class="btn-submit" @click="showModal = true">
       
    </button> -->
  </div>

  <!-- Modal Window -->
  <div v-if="showModal" class="modal-overlay">
    <div class="modal-content">
      <h3 class="modal-title">
        {{ errorMessage ? "" : " " }}
      </h3>
      <div class="modal-body">
        <div v-if="!errorMessage" class="confirmation-message">
          <i class="fas fa-question-circle message-icon"></i>
          <p> ,       ?</p>
        </div>
        <div v-else class="error-container">
          <i class="fas fa-exclamation-circle error-icon"></i>
          <p class="error-message">{{ errorMessage }}</p>
        </div>
      </div>
      <div class="modal-buttons">
        <button
          v-if="!errorMessage"
          class="btn-confirm"
          @click="submitResponse"
          :disabled="isLoading"
        >
          <i v-if="isLoading" class="fas fa-spinner fa-spin"></i>
          <i v-else class="fas fa-check"></i>
          {{ isLoading ? "..." : "" }}
        </button>
        <button class="btn-cancel" @click="closeModal">
          <i class="fas fa-times"></i> {{ errorMessage ? "" : "" }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useProfileStore } from "@/store/profileStore";
import { useUserStore } from "@/store/user";
import { useProjectStore } from "@/store/projects";

export default {
  setup() {
    const profileStore = useProfileStore();
    const userStore = useUserStore();
    const projectStore = useProjectStore();

    return {
      profileStore,
      userStore,
      projectStore,
    };
  },
  data() {
    return {
      showModal: false,
      selectedProjectUuid: null,
      errorMessage: null,
      isLoading: false,
    };
  },
  computed: {
    allProjects() {
      return this.projectStore.getProjects.filter(project => project.status === "auction");
    },
    isGip() {
      return this.userStore.isGip;
    },
    userSpecialty() {
      return this.profileStore.profileData.physical_profile?.specialty;
    },
    getCurrentSpecialty() {
      if (this.userStore.isGip) {
        return '';
      }
      
      const specialtyId = this.profileStore.profileData.physical_profile?.specialty;
      const specialty = this.profileStore.specialties.find(s => s.id === specialtyId);
      return specialty ? specialty.name : ' ';
    }
  },
  methods: {
    formatDate(dateString) {
      try {
        const formattedDate = new Date(dateString).toLocaleDateString("ru-RU", {
          day: "numeric",
          month: "long",
          year: "numeric",
        });
        return formattedDate;
      } catch (error) {
        console.error("Date formatting error:", error);
        return dateString;
      }
    },
    async submitResponse() {
      this.isLoading = true;
      try {
        const accessToken = localStorage.getItem("accessToken");
        if (!accessToken) {
          throw new Error(" ");
        }

        await axios.post(
          `api/projects/responses/?project_uuid=${this.selectedProjectUuid}`,
          {
            message: "    ",
          },
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
              "Content-Type": "application/json",
            },
          },
        );

        this.closeModal();
      } catch (error) {
        console.error("   :", error);

        let errorMessage = "    ";
        if (error.response?.data?.detail) {
          errorMessage = error.response.data.detail;
        } else if (error.response?.data?.error) {
          errorMessage = error.response.data.error;
        }

        this.errorMessage = errorMessage;
      } finally {
        this.isLoading = false;
      }
    },
    closeModal() {
      this.showModal = false;
      this.errorMessage = null;
    },
    selectProject(uuid) {
      this.selectedProjectUuid = uuid;

      if (!localStorage.getItem("accessToken")) {
        this.$router.push("/login");
        return;
      }

      this.showModal = true;
    },
    getSpecialtyName(specialtyId) {
      const specialtyMap = {
        1: '',
        2: '-',
        3: ' ',
        4: ' ',
        5: '  ',
        6: '  ',
        7: ' '
      };
      return specialtyMap[specialtyId] || ` ${specialtyId}`;
    },
    formatBudget(budget) {
      return Number(budget).toLocaleString('ru-RU');
    },
    filteredBudgets(budgets) {
      if (this.isGip) {
        return budgets;
      } else {
        return budgets.filter(budget => budget.specialty === this.userSpecialty);
      }
    },
  },
  async created() {
    console.log("AuctionContent: Component created");
    await this.projectStore.fetchProjects();
    console.log("AuctionContent: Projects loaded:", this.allProjects);
  },
};
</script>

<style scoped>
.content-auction {
  min-height: 90vh;
  background-color: #e2f3ff;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-container {
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.header {
  background: white;
  padding: 30px;
  border-bottom: 1px solid #e0e0e0;
}

.title-section {
  position: relative;
  margin-bottom: 20px;
}

.project-title {
  font-size: 28px;
  color: #2c3e50;
  margin: 0 0 10px 0;
  font-weight: 600;
}

.project-subtitle {
  display: block;
  font-size: 16px;
  color: #6c757d;
  margin-bottom: 10px;
}

.category {
  display: inline-block;
  background-color: #539bff;
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
  box-shadow: 0 2px 4px rgba(83, 155, 255, 0.2);
}

.meta-info {
  display: flex;
  gap: 30px;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #495057;
  font-size: 15px;
}

.content-section {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  padding: 30px;
}

.description {
  background: white;
  padding: 25px;
  border-radius: 12px;
  border: 1px solid #e0e0e0;
}

.description h3 {
  color: #2c3e50;
  margin-bottom: 15px;
  font-size: 20px;
}

.description p {
  color: #495057;
  line-height: 1.6;
}

.proposal {
  background: white;
  padding: 25px;
  border-radius: 12px;
  border: 1px solid #e0e0e0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.budget-section h3 {
  color: #2c3e50;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 600;
}

.specialty-budgets {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.specialty-budget {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #e0e0e0;
}

.specialty-budget:last-child {
  border-bottom: none;
}

.specialty-name {
  color: #495057;
  font-size: 16px;
}

.budget {
  color: #1a202c;
  font-size: 18px;
  font-weight: 600;
}

.position-section h3 {
  color: #2c3e50;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 600;
}

.position {
  display: block;
  color: #495057;
  font-size: 16px;
}

.btn-submit {
  margin-top: auto;
  padding: 12px 24px;
  background-color: #539bff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.btn-submit:hover {
  background-color: #4285f4;
}

.footer {
  padding: 20px 30px;
  border-top: 1px solid #e0e0e0;
  color: #6c757d;
  font-size: 14px;
  text-align: right;
}

.no-projects {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 300px);
  font-size: 16px;
  color: #777777;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(3px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1001;
}

.modal-content {
  background-color: white;
  padding: 30px;
  border-radius: 15px;
  width: 400px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  max-width: 400px;
  word-wrap: break-word;
}

.modal-title {
  color: #2c3e50;
  font-size: 24px;
  margin: 0 0 20px 0;
  text-align: center;
  font-weight: 600;
}

.modal-body {
  margin-bottom: 25px;
}

.confirmation-message,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 15px;
}

.message-icon {
  font-size: 48px;
  color: #539bff;
}

.error-icon {
  font-size: 48px;
  color: #dc3545;
}

.error-message {
  color: #dc3545;
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
}

.modal-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 25px;
}

.btn-confirm,
.btn-cancel {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
}

.btn-confirm {
  background-color: #539bff;
  color: white;
  border: none;
}

.btn-confirm:hover {
  background-color: #4285f4;
  transform: translateY(-1px);
}

.btn-cancel {
  background-color: #f8f9fa;
  color: #2c3e50;
  border: 1px solid #dee2e6;
}

.btn-cancel:hover {
  background-color: #e9ecef;
  transform: translateY(-1px);
}
</style>
